import React, { Component } from 'react';
import moment from 'moment';
import DatePicker from 'react-date-picker';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import step3 from './step3';
const options = [
  { value: 'Parents', label: 'Parents' },
  {
    value: 'Spouse',
    label: 'Spouse'
    //className: 'myOptionClassName'
  },
  { value: 'Siblings', label: 'Siblings' },
  { value: 'Others', label: 'Others' }
];
//const x = document.getElementById("drop").value
// var eID = document.getElementById("drop")
// var dayVal = eID.options[eID.selectedIndex].value
// var daytxt = eID.options[eID.selectedIndex].text

const defaultOption = 'Relationship';
export default class AddUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      firstName: '',
      lastName: '',
      relationship: '',
      dob: new Date(),
      getattendeedata: {},
      attributeForm: [],
      submitFirstName: '',
      submitLastName: '',
      submitRelation: '',
      dateInNum: ''
    };
    //this.addAttributeForm();
  }
  onChangeDate = date =>
    this.setState({ date }, () => {
      this.changeDateInNum(this.state.date);
    });
  changeDateInNum = date => {
    var d = moment(date);
    var datevalue = d.format('YYYY-MM-DD');
    this.setState({ dob: datevalue });
  };

  onChangeHandler = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  handleRemoveItem = () => {
    this.props.removeItem(this.props.index);
  };

  handleSaveItem = () => {
    if (this.validateFields()) {
      const obj = {
        firstName: this.state.firstName,
        lastName: this.state.lastName,
        relationship: this.state.relationship,
        dob: this.state.dob,
        isSelected: true
      };
      this.props.onSaveBtnClick(this.props.index, obj);
    }
  };

  validateFields = () => {
    if (this.state.firstName.length === 0) {
      this.showErrorAlert('First Name cannot be empty');
      return false;
    }
    if (this.state.lastName.length === 0) {
      this.showErrorAlert('Last Name cannot be empty');
      return false;
    }

    if (this.state.relationship === '') {
      this.showErrorAlert('Select Relationship of person');
      return false;
    }

    if (this.state.dob.length === 0) {
      this.showErrorAlert('Select DOB pf person');
      return false;
    }
    return true;
  };

  showErrorAlert = message => {
    alert(message);
  };

  render() {
    return (
      <div className="form">
        <h5 className="text-primary">New Person</h5>

        <div className="row">
          <div className="col-lg-6">
            <div className="form-group">
              <input
                type="text"
                className="form-control"
                placeholder="First Name"
                name="firstName"
                id="firstName"
                value={this.state.firstName}
                onChange={this.onChangeHandler}
              />
            </div>
          </div>
          <div className="col-lg-6">
            <div className="form-group">
              <input
                type="text"
                className="form-control"
                placeholder="Last Name"
                name="lastName"
                id="lastName"
                value={this.state.lastName}
                onChange={this.onChangeHandler}
              />
            </div>
          </div>
          <div className="col-lg-6">
            <div className="form-group">
              <select
                className="form-control"
                id="relation"
                placeholder="Relationship"
                name="relationship"
                onChange={this.onChangeHandler}
              >
                <option value="" disabled selected>
                  Relationship
                </option>
                <option value="Parents">Parents</option>
                <option value="Family">Family</option>
                <option value="Spouse">Spouse</option>
                <option value="Siblings">Siblings</option>
                <option value="Others">Others</option>
              </select>
            </div>
          </div>
          <div className="col-lg-6">
            <div className="form-group">
              <DatePicker
                onChange={this.onChangeDate}
                maxDate={new Date()}
                value={this.state.dob}
              />
            </div>
          </div>
          <div className="col-lg-12">
            <div className="form-group add-another">
              <button
                className="btn btn-success mr-2"
                onClick={this.handleSaveItem}
              >
                Save &nbsp;&nbsp;&nbsp;&nbsp;
                <i className="fas fa-angle-right" />
              </button>
              <button
                className="btn btn-success"
                onClick={this.handleRemoveItem}
              >
                Remove &nbsp;&nbsp;&nbsp;&nbsp;
                <i className="fas fa-angle-right" />
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
