import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import { asyncLocalStorage } from '../../common/helper';

import './header.css';
class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            width: 0,
            height: 0,
            isloginState: null,
            isProviderClick: false
        };
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }

    componentDidMount() {
        console.log(this.state.isloginState);
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);

    }
    componentWillMount() {
        if (localStorage.getItem('token') !== '') {
            this.setState({ isloginState: true });
        } else {
            this.setState({ isloginState: false });
        }
    }
    logoutHandler = () => {
        console.log('called here');
        asyncLocalStorage.setItem('token', '').then(() => {
            this.setState({ isloginState: false });
        });
        //window.localStorage.clear()
        // localStorage.clear();
        // this.setState({
        //     isloginState: false
        // },<Redirect to={"/login"} />
        //     //,()=>this.signOut()
        // )
        //this.props.IsLogoutHandler()
    };

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }
    toggleMenu() {
        // console.log(this.state.width, this.state.height);
        if (window.innerWidth < 991) {
            console.log(
                document
                    .getElementById('navbarSupportedContent')
                    .className.search('show')
            );
            if (
                document
                    .getElementById('navbarSupportedContent')
                    .className.search('show') < 0
            ) {
                document.getElementById('navbarSupportedContent').className =
                    document.getElementById('navbarSupportedContent').className + ' show';
            } else {
                document.getElementById('navbarSupportedContent').className =
                    'collapse navbar-collapse';
            }
        }
    }

    render() {

        return (
            <React.Fragment>
                <header
                    className="HJ_home">
                    <nav className="navbar navbar-expand-lg navbar-dark bg-light">
                        <button
                            className="navbar-toggler"
                            type="button"
                            data-toggle="collapse"
                            data-target="#navbarSupportedContent"
                            aria-controls="navbarSupportedContent"
                            aria-expanded="false"
                            aria-label="Toggle navigation"
                            onClick={this.toggleMenu}
                        >
                            <span className="navbar-toggler-icon" />
                        </button>
                        <div
                            className="collapse navbar-collapse"
                            id="navbarSupportedContent"
                        >
                            <ul className="navbar-nav mr-auto">
                                <li className="nav-item active">
                                    <Link className="nav-link" to="/work">
                                        How It Works1
                      </Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/faq" className="nav-link">
                                        FAQs
                      </Link>
                                </li>
                                <li className="nav-item dropdown">
                                    <a
                                        className="nav-link dropdown-toggle"
                                        href="#"
                                        id="navbarDropdown"
                                        role="button"
                                        data-toggle="dropdown"
                                        aria-haspopup="true"
                                        aria-expanded="false"
                                    >
                                        Resource Center
                      </a>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/aboutus">
                                        About
                      </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/contact">
                                        Contact
                      </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/login">
                                        Login
                      </Link>
                                </li>
                            </ul>


                            <form className="form-inline my-2 my-lg-0 dropdown">

                                <div>
                                    <Link
                                        className="btn btn-primary btn-white active dropdown-toggle"
                                        to="/landingpage"
                                        role="button">
                                        For Providers
                                    </Link>
                                </div>
                            </form>
                        </div>
                    </nav>
                </header>
            </React.Fragment>
        );
    }
}

export default Header;
