import React, { Component } from 'react';
import DatePicker from 'react-date-picker';
import moment from 'moment'
export default class AddFamily extends Component {
    constructor(props){
        super(props)
        this.state={
            date: new Date(),
            firstName:"",
            lastName:"",
            relationship:"",
            dob:"",
            getattendeedata:{},
            attributeForm: [],
            submitFirstName:"",
            submitLastName:"",
            submitRelation:"",
            dateInNum:""
        }

    }

    onChangeDate = date => this.setState({ date },()=>{
        this.changeDateInNum(this.state.date)
    })
    changeDateInNum = (date) => {
      var d = moment(date)
      var datevalue = d.format("YYYY-MM-DD")
      this.setState({dateInNum:datevalue},()=>{
        console.log("date in num is",this.state.dateInNum)
      })
    }

    onChangeHandler = (e) => {
    this.setState({[e.target.name]:e.target.value})

    }
    onSubmitHandler = (e) => {
        e.preventDefault();
        this.getDataHandler();
        this.props.removeElementHandler(e);
    }
    getDataHandler = () => {
        //e.preventDefault();
        var userdata = 
        {firstName : document.getElementById('firstName').getAttribute('value'),
        lastName :  document.getElementById('lastName').getAttribute('value'),
        dob:this.state.dateInNum,
        relationship: document.getElementById('relation').options[document.getElementById('relation').selectedIndex].text

        }
        console.log("userdata value ",userdata)
        if((userdata.firstname && userdata.lastname)!== ""){
            this.props.arraydataHandler(userdata)  
        }
        else{
            alert("Fields cannot be blank")
        }
    }



    
    render() {
       

        return (
                <div className="col-md-6">
                    <p className="text-primary text-left">Person  <a href="#" className="text-danger pull-right" onClick ={this.props.removeElementHandler}><i className="fa fa-times-circle "></i></a></p>
                    <div className="row">
                        <div className="col-sm-12">
                            <div className="form-group">
                            <input type="text" className="form-control" placeholder="First Name" name="firstName" id='firstName' value={this.state.firstName} onChange={this.onChangeHandler}/>
                            </div>
                        </div>
                        <div className="col-sm-12">
                            <div className="form-group">
                            <input type="text" className="form-control" placeholder="Last Name"  name="lastName" id='lastName' value={this.state.lastName} onChange={this.onChangeHandler}/>
                            </div>
                        </div>
                        <div className="col-sm-6">
                            <div className="form-group">
                            <DatePicker
                            onChange={this.onChangeDate}
                            value={this.state.date} />
                            </div>
                        </div>
                        <div className="col-sm-6">
                            <div className="form-group">
                                <select className="form-control" id="relation" >
                                    <option value="" disabled selected>Relationship</option>
                                    <option value="Parents">Parents</option>
                                    <option value="Family">Family</option>
                                    <option value="Spouse">Spouse</option>
                                    <option value="Siblings">Siblings</option>
                                    <option value="Others">Others</option>
                                </select>
                            </div>
                        </div>
                        <div>
                        <button className="btn btn-success" onClick={this.onSubmitHandler}>Save &nbsp;&nbsp;&nbsp;&nbsp;<i className="fas fa-angle-right"></i></button>
                        </div>
                    </div>
                </div>


        );
    }
}



