import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './profile-sidebar.css';

class ProfileSidebar extends Component {
    constructor(props) {
        super(props)
        this.state = {
            activeTab: 1
        }
        this.sidebarChange = this.sidebarChange.bind(this);
        this.sidebarChange(1);
    }

    closeSidebar() {
        alert();
    }
    sidebarChange = (tab) => {
        this.activeTab = tab;
    }
    render() {
        return (
            <article className="profile-sidebar open">

                <ul className="nav navbar">
                    <li className={this.activeTab == 1 ? 'active nav-items' : 'nav-items'}><Link to="/provider" onClick={() => this.sidebarChange(1)}><i className="fas fa-home"></i>Dashboard</Link></li>
                    <li className={this.activeTab == 2 ? 'active nav-items' : 'nav-items'}><Link to="/search" onClick={() => this.sidebarChange(2)}><i className="fas fa-search"></i>Provider Search</Link></li>
                    <li className={this.activeTab == 3 ? 'active nav-items' : 'nav-items'}><Link to="/setting" onClick={() => this.sidebarChange(3)}><i className="fas fa-user"></i>My Profile</Link></li>
                    <li className={this.activeTab == 4 ? 'active nav-items' : 'nav-items'}><Link to="/setting" onClick={() => this.sidebarChange(4)}><i className="far fa-calendar-alt"></i>My Appointments</Link></li>
                    <li className={this.activeTab == 5 ? 'active nav-items' : 'nav-items'}><Link to="/setting" onClick={() => this.sidebarChange(5)}><i className="fas fa-dollar-sign"></i>Billing</Link></li>
                    <li className={this.activeTab == 6 ? 'active nav-items' : 'nav-items'}><Link to="/setting" onClick={() => this.sidebarChange(6)}><i className="fas fa-cog"></i>Settings</Link></li>
                </ul>
            </article>
        );
    }
}

export default ProfileSidebar;