export function GetAuth(type) {
    let BaseURL = ' http://66.175.217.67:8443/api/';
    const token = localStorage.getItem('token')

    return new Promise((resolve, reject) => {
        fetch(BaseURL + type, {
            method: 'GET',
            headers:
                { 'Content-Type': 'application/json', 'authenticate': token }
        })
            .then((response) => response.json())
            .then((res) => {
                resolve(res);
            })
            .catch((error) => {
                reject(error);
                console.log("error is")
            });
    });
}