import React, { Component } from 'react';
import './top_banner.css';
class BannerTop extends Component {
    constructor(props) {
        super(props);

    }
    state = {
        bannerData: {
            title: this.props.bannerData.title,
            subtitle: this.props.bannerData.subTitle,
            img: this.props.bannerData.img
        }
    }
    render() {
        return (
            <div className="top-banner" style={{ backgroundImage: 'url(/images/' + this.state.bannerData.img + ')' }}>
                <div className="text-center ">
                    <h1>{this.state.bannerData.title}</h1>
                    <h6>{this.state.bannerData.subtitle}</h6>
                </div>
            </div>
        );
    }
}

export default BannerTop;