import React, { Component } from 'react';
import ProfileSidebar from '../../component/profile-sidebar/profile-sidebar';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { Link } from 'react-router-dom';
import './pdprofile.css';
import Reset_password from './reset_password';
import Provider_family from './family';
import Setting_provider from './pdsetting';
import Billing_provider from './billing';
import ProfileEdit from './profile_edit';
import { withRouter } from 'react-router';
import { SERVER_URL, TOKEN_DATA, USER_DATA } from '../../common/Constant';
import { asyncLocalStorage } from '../../common/helper';
// import ImagePicker from 'react-image-picker'
// import 'react-image-picker/dist/index.css'
import Header from '../../component/header';

class SettingUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstName: '',
      lastName: '',
      city: '',
      locality: '',
      country: '',
      pictures: [],
      fil: [],
      profilePic: 'url(/images/client1.png)',
      userImage: '',
      token: '',
      isUpdated: false
    };
  }
  onDrop = (pictureFiles, pictureDataURLs) => {
    this.setState(
      {
        pictures: this.state.pictures.concat(pictureFiles)
      },
      () => {
        console.log('pictire', this.state.pictures);
      }
    );
  };
  componentDidMount() {
    asyncLocalStorage.getItem(TOKEN_DATA).then(token => {
      asyncLocalStorage
        .getItem(USER_DATA)
        .then(goals => {
          let profile_data = JSON.parse(goals);
          this.updateFields(token, profile_data);

          //this callback is executed when your Promise is resolved
        })
        .catch(error => {
          //this callback is executed when your Promise is rejected
        });
    });
  }

  isUpdatedHandler = () => {
    console.log('isupdated handler called........ hahahahha');
    asyncLocalStorage.getItem(TOKEN_DATA).then(token => {
      asyncLocalStorage
        .getItem(USER_DATA)
        .then(goals => {
          let profile_data = JSON.parse(goals);
          this.updateFields(token, profile_data);

          //this callback is executed when your Promise is resolved
        })
        .catch(error => {
          //this callback is executed when your Promise is rejected
        });
    });
  };

  updateFields = (token, profile_data) => {
    this.setState({
      loading: false,
      profile_data,
      token,
      userImage:
        profile_data.profileimage != undefined
          ? SERVER_URL + 'user/image/' + profile_data.profileimage
          : '/images/client1.png',
      userFirstName: profile_data.firstName,
      userLastName: profile_data.lastName,
      userAddressApartment:
        profile_data.address != undefined ? profile_data.address.apartment : '',
      userAddressCity:
        profile_data.address != undefined ? profile_data.address.city : '',
      userAddressState:
        profile_data.address != undefined ? profile_data.address.state : '',
      userAddressCountry:
        profile_data.address != undefined ? profile_data.address.country : ''
    });
  };

  handleFileChange = event => {
    const { target } = event;
    const { files } = target;

    if (files && files[0]) {
      var reader = new FileReader();

      reader.onloadstart = () => this.setState({ loading: true });

      reader.onload = event => {
        this.setState(
          {
            file: files[0],
            data: event.target.result,
            loading: false,
            profilePic: event.target.result
          },
          () => {
            this.postPicture()
              .then(data => data.json())
              .then(data => {
                if (data.success) {
                  asyncLocalStorage
                    .setItem(USER_DATA, JSON.stringify(data.data))
                    .then(dataTemp => {
                      global.profileUpdated = true;
                      this.updateFields(this.state.token, data.data);
                      // this.setState({ loading: false })
                    })
                    .catch(err => {
                      this.setState({ loading: false });
                    });
                } else {
                  this.setState({ loading: false });
                }
              })
              .catch(error => {
                console.log('Image upload error', error);
              });
          }
        );
      };
      reader.readAsDataURL(files[0]);
    }
  };

  postPicture = () => {
    const apiUrl = `${SERVER_URL}user/uploadimage`;
    // const uri = this.state.profileImage.uri;
    // const uriParts = uri.split('.');
    // const fileType = uriParts[uriParts.length - 1];
    const formData = new FormData();
    formData.append('profileimage', this.state.file);
    const options = {
      method: 'POST',
      body: formData,
      headers: {
        Accept: 'application/json',
        authenticate: this.state.token
      }
    };
    return fetch(apiUrl, options);
  };

  render() {
    console.log("The image is: ", this.state.userImage);
    return (
      <div>
        <Header />

        <div className="provider-profile setting-page">
          <div className="sidebar-provider ">
            <ProfileSidebar />
            <div className="inner-content">
              <div className="container-fluid">
                <br />
                <div className="text-left top-links-section">
                  <div className="row">
                    <div className="col-sm-6">
                      {' '}
                      <Link to="">
                        <i className="fa fa-angle-left" /> &nbsp;Back
                      </Link>
                      <h3 className="title">
                        {this.state.userFirstName} {this.state.userLastName}
                      </h3>
                      <p>
                        {this.state.userAddressCity},{' '}
                        {this.state.userAddressCountry}
                      </p>
                    </div>
                    <div className="col-sm-6 text-right uplodeFile_vd">
                      {/* <figure className="profile-img"
                                            style={{ backgroundImage: 'url(/images/client1.png)' }}> */}
                      <img
                        className="profile-img"
                        id="target"
                        src={this.state.userImage}
                      />
                      <div>
                        <label htmlFor="car" className="imageUploadLabel">
                          <i className="fas fa-upload mr-2" /> Upload
                        </label>
                        <input
                          style={{
                            display: 'inline-block',
                            width: 150
                          }}
                          id="car"
                          type="file"
                          accept="image/*"
                          capture="camera"
                          style={{ visibility: 'hidden' }}
                          onChange={this.handleFileChange}
                        />
                        {/* <div>
                          <input
                            style={{
                              display: 'inline-block',
                              width: 150
                            }}
                            id="car"
                            type="file"
                            accept="image/*"
                            capture="camera"
                            onChange={this.handleFileChange}
                          />
                        </div> */}

                        {/* </figure> */}
                      </div>
                    </div>
                  </div>
                  <div className="setting-tab">
                    <Tabs>
                      <TabList>
                        <Tab>Profile</Tab>
                        <Tab>Family</Tab>
                        <Tab>Billing</Tab>
                        <Tab>Settings</Tab>
                        <Tab>Password</Tab>
                      </TabList>
                      <TabPanel>
                        <div className="container-fluid">
                          <ProfileEdit
                            isUpdatedHandler={this.isUpdatedHandler}
                            showDetailsOfUser={this.showDetailsOfUser}
                          />
                        </div>
                      </TabPanel>
                      <TabPanel>
                        <div className="container-fluid">
                          <Provider_family />
                        </div>
                      </TabPanel>
                      <TabPanel>
                        <div className="container-fluid">
                          <Billing_provider />
                        </div>
                      </TabPanel>
                      <TabPanel>
                        <div className="container-fluid">
                          <Setting_provider />
                        </div>
                      </TabPanel>
                      <TabPanel>
                        <div className="container-fluid">
                          <Reset_password />
                        </div>
                      </TabPanel>
                    </Tabs>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(SettingUser);
