import React, { Component } from 'react'
import ReactStars from 'react-stars';
import Modal from '../../common/Modal/Modal';

class StarRatingComponent extends Component {
    constructor(props) {
        super(props)
        this.state = {
            toggleExpert: 'expert',
            profiledata: {},
            show: false,
            showTwo: false,
            star1: 0,
            star2: 0,
            star3: 0,
            totalstar: null,
            isLogin: null
        }
        // this.expertTgl = this.expertTgl.bind(this);
    }

    componentDidMount() {
        this.setState({
            show: this.props.show
        })
    }
    componentWillReceiveProps(nextProps) {

        if (nextProps != undefined) {
            this.setState({
                show: nextProps.show
            })
        }
    }
    hideModal = () => {
        this.setState({
            show: !this.state.show
        });
    }
    render() {
        return (
            <Modal show={this.state.show} handleClose={this.hideModal}>
                <div className="List_left">
                    <ul>
                        <li>
                            <span className="text_bx">Lorem ipsum dolor sit amet, consectetur adipiscing elit?</span>
                            {/* <span className="imgbx"><img src="/images/big-star.png"/></span> */}
                            <span className="imgbx">
                                <ReactStars
                                    count={5}
                                    value={this.state.star1}
                                    onChange={this.rateFirstField}
                                    size={24}
                                    color2={'#ffd700'} />
                            </span>
                        </li>
                        <li>
                            <span className="text_bx">Pellentesque bibendum euismod lacus in Fringilla?</span>
                            <span className="imgbx">
                                <ReactStars
                                    count={5}
                                    value={this.state.star2}
                                    onChange={this.rateSecondField}
                                    size={24}
                                    color2={'#ffd700'} />
                            </span>
                        </li>
                        <li>
                            <span className="text_bx">Nullam sollicitudin auctor ex molestie suscipit?</span>
                            <span className="imgbx">
                                <ReactStars
                                    count={5}
                                    value={this.state.star3}
                                    onChange={this.rateThirdField}
                                    size={24}
                                    color2={'#ffd700'} />
                            </span>
                        </li>
                        <li>
                            <button onClick={this.rateTheProviderHandler}>Submit</button>
                        </li>
                    </ul>
                </div>

            </Modal>
        )
    }
}

export default StarRatingComponent