import React, { Component } from 'react';
import './alert.css';

class Alert extends Component {
  render() {
    if (this.props.error) {
      //show the error message here.
      return (
        <div
          className="error-message-box text-left"
          style={{ display: this.props.display }}
        >
          <span className="alert-error ml-2">
            <i className="fa fa-times" />
          </span>
          <span className="error-detail">{this.props.message}</span>
        </div>
      );
    }
    return (
      <div
        className="alert-message-box  text-left"
        style={{ display: this.props.display }}
      >
        <span className="alert-success">
          <i className="fa fa-check" />
        </span>
        <span className="alert-detail-signup">{this.props.message}</span>
      </div>
    );
  }
}

export default Alert;
