import React, { Component } from 'react';
import ReactStars from 'react-stars';
import { Redirect, Link } from 'react-router-dom';
import { withRouter } from 'react-router';
import { Map, InfoWindow, Marker, GoogleApiWrapper } from 'google-maps-react';
// import { TOKEN_DATA, USER_DATA } from '../../Utility/Constant'
import Alert from '../../common/Alert';
import { IS_LOGGED_IN } from '../../common/Constant';
import SendMessageModal from './SendMessageModal'

class ProviderSingleItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      providerFirstName: '',
      providerMiddleName: '',
      providerLastName: '',
      providerProfile: '',
      providerRatingNumber: 0,
      providerRatingStar: 0,
      providerEmail: '',
      providerPhoneNumber: '',
      providerAddressRegion: '',
      providerAddressformatted: '',
      providerAddressCountry: '',
      jobSpecs: '',
      providerData: {},
      payment_options: [],
      insurance_accepted: [],
      phone: '',
      display: 'none',
      error: false,
      message: '',
      modalIsOpen: false
    };
  }
  navigateToProviderProfile = val => {
    this.props.navigationDrawer.navigate('ProfileBookNavigator', {
      provider_id: this.state.provider_id,
      navigationDrawer: this.props.navigationDrawer
    });
    // this.props.navigation.navigate('ProviderProfileScreen', {
    //     provider_id: this.state.provider_id,
    //     navigationDrawer: this.props.navigationDrawer
    // })
  };

  componentDidMount() {
    if (this.props.data) {
      this.updateFields(this.props.data);
    }
  }
  componentWillReceiveProps(nextProps) { }

  updateFields = data => {
    let checkAddressAvailable =
      Array.isArray(data.address) && data.address.length > 0;
    this.setState({
      providerData: data,
      provider_id: data._id,
      providerFirstName: data.firstName != undefined ? data.firstName : '',
      providerMiddleName: data.middleName != undefined ? data.middleName : '',
      providerLastName: data.lastName != undefined ? data.lastName : '',
      providerProfile: '',
      providerRatingNumber: data.totalrating,
      providerRatingStar: data.totalstar,
      providerAddressRegion: checkAddressAvailable
        ? data.address[0].region
        : '',
      providerAddressformatted: checkAddressAvailable
        ? data.address[0].formatted
        : '',
      providerAddressCountry: checkAddressAvailable
        ? data.address[0].country
        : '',
      payment_options: data.payment_options.toString(),
      insurance_accepted: data.insurance_accepted.toString(),
      primaryLatitude: checkAddressAvailable ? data.address[0].location[0] : 0,
      primarLongitude: checkAddressAvailable ? data.address[0].location[1] : 0,
      phone: data.phone.toString()
    });
  };

  onBookAppointmentClick = async () => {
    this.props.history.push({
      pathname: '/appointment/' + this.state.provider_id,
      state: {
        providerData: this.state.providerData,
        provider_id: this.state.provider_id
      }
    });
  };
  showToast = () => {
    this.setState({
      display: 'block',
      error: true,
      message: 'You need to login'
    });
    setTimeout(() => {
      this.setState({
        display: 'none',
        error: true,
        message: 'You need to login'
      });
    }, 1000);
  };

  closeModal = () => {
    this.setState({ modalIsOpen: false });
  }
  messageSendSuccess = (message) => {
    this.setState({
      display: 'block',
      error: false,
      message: message,
      modalIsOpen: false
    });
    setTimeout(() => {
      this.setState({
        display: 'none',
        error: true,
        message: 'You need to login'
      });
    }, 1500);
  }
  render() {
    return (
      <div>
        <SendMessageModal
          modalIsOpen={this.state.modalIsOpen}
          closeModal={this.closeModal}
          providerData={this.state.providerData}
          messageSendSuccess={this.messageSendSuccess}
        />
        <Alert
          display={this.state.display}
          error={this.state.error}
          message={this.state.message}
        />
        <div className="row"> 
          <div className = "col-md-3 media-left" >
            <Link
              to={{
                pathname: `/provider/${this.state.provider_id}`,
                state: { userid: this.state.provider_id }
              }}
            >
              <figure style={{ backgroundImage: 'url(/images/client_1.png)', paddingBlockEnd: 10, height:'180px' }}>
                <a href="#">
                  <img src={'/images/green_check_icon.png'} />{' '}
                </a>
              </figure>
            </Link>
          </div>
          <div className="col-md-6 medi-body">
            <div className="searchList-content">
              <h2>
                {this.state.providerFirstName} {this.state.providerMiddleName}{' '}
                {this.state.providerLastName}
              </h2>
              <p>
                {this.state.providerAddressRegion},
                {this.state.providerAddressCountry}
              </p>
              <p className="active">{this.state.insurance_accepted}{' '}{this.state.payment_options}</p>
              <div className="row help-search">
                <div className="col-md-2">
                  <ReactStars
                    count={5}
                    value={
                      this.state.providerRatingNumber
                    }
                    size={15}
                    edit={false}
                    color2={'#ffd700'}
                  />
                </div>
                <div className="col-md-2 rating">
                  <span>{this.state.providerRatingNumber} Ratings</span>
                </div>
              </div>
              <div className="app">
                <label>Next Available New Patient Appointment</label>
                <p>
                  <span>
                    <i className="fas fa-calendar" />
                    &nbsp;October 12, 2018
                  </span>
                  &nbsp;&nbsp;&nbsp;
                  <span>
                    <i className="fas fa-clock" />
                    &nbsp;10:00 am
                  </span>
                </p>
              </div>
            </div>
          </div>
        </div>

        <ul className="media">
          <li className="media-left">
            <Link
              to={{
                pathname: `/provider/${this.state.provider_id}`,
                state: { userid: this.state.provider_id }
              }}
            >
              <figure style={{ backgroundImage: 'url(/images/client_1.png)', paddingBlockEnd: 10 }}>
                <a href="#">
                  <img src={'/images/green_check_icon.png'} />{' '}
                </a>
              </figure>
              < button className = "btn bnt-filter complete-profile-button" >
                {' '}
                <img src={'/images/green_icon.png'} />
                &nbsp;View Profile{' '}
              </button>
            </Link>
          </li>
          <li className="media-body text-left">
            <div className="searchList-content">
              <h2>
                {this.state.providerFirstName} {this.state.providerMiddleName}{' '}
                {this.state.providerLastName}
              </h2>
              <p>
                {this.state.providerAddressRegion},
                {this.state.providerAddressCountry}
              </p>
              <p className="active">{this.state.insurance_accepted}{' '}{this.state.payment_options}</p>
              <div className="row help-search">
                <div className="col-md-2">
                  <ReactStars
                    count={5}
                    value = {
                      this.state.providerRatingNumber
                    }
                    size={15}
                    edit={false}
                    color2={'#ffd700'}
                  />
                </div>
                <div className="col-md-2 rating">
                    <span>{this.state.providerRatingNumber} Ratings</span>              
                </div>
              </div>
              <div className="app">
                <label>Next Available New Patient Appointment</label>
                <p>
                  <span>
                    <i className="fas fa-calendar" />
                    &nbsp;October 12, 2018
                  </span>
                  &nbsp;&nbsp;&nbsp;
                  <span>
                    <i className="fas fa-clock" />
                    &nbsp;10:00 am
                  </span>
                </p>
              </div>
              <div className="btn-wrapper">
              <div className="row">
                <div className="col-md-6">
                  <button
                    className = "btn btn-primary half-complete-profile-button"
                    onClick={this.onBookAppointmentClick}
                  >
                  Book An Appointment
                  </button>
                </div>
                <div className="col-md-6">
                  < button className = "btn btn-success half-complete-profile-button"
                  onClick = {
                    () => this.setState({
                      modalIsOpen: true
                    })
                  } >
                    <i className="fas fa-envelope" />
                    &nbsp;Message Me
                  </button>
                </div>
              </div>
              </div>
            </div>
          </li>
          <li className="media-right">
            <figure>
              <div className="map-cotainer">
                <Map
                  google={this.props.google}
                  style={{ height: '100%', width: '100%' }}
                  zoom={12}
                >
                  <Marker
                    title={'The marker`s title will appear as a tooltip.'}
                    name={'SOMA'}
                    position={{
                      // lat: this.state.primaryLatitude,
                      // lng: this.state.primarLongitude
                      lat: 37.778519,
                      lng: -122.40564
                    }}
                  />
                </Map>
              </div>
              <figcaption className="text-left">
                <label className="title" style={{ minWidth: 200 }}>
                  {this.state.phone}
                </label>
                <p>{this.state.providerAddressformatted}</p>
                <p>
                  {this.state.providerAddressRegion}{' '}
                  {this.state.providerAddressCountry}
                </p>
              </figcaption>
            </figure>
          </li>
        </ul>
        {/* <Therapist user="dada"
                                //userId= {node._id}
                                /> */}
      </div>
    );
  }
}

// export default ProviderSingleItem
export default GoogleApiWrapper({
  apiKey: 'AIzaSyDeOma3Mu0HcYWBgEGdnymVoirLQkzJ0-A'
})(withRouter(ProviderSingleItem));
