import React, { Component } from 'react';
import DashboardPageHeader from './DashboardPageHeader'
import HomePageHeader from './HomePageHeader'
import LoginPageHeader from './LoginPageHeader'
import { IS_LOGGED_IN } from '../../common/Constant'
import { asyncLocalStorage } from '../../common/helper'
export default class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isLogined: false
        };
    }
    componentDidMount() {
        asyncLocalStorage.getItem(IS_LOGGED_IN)
            .then(data => {
                if (data == 'true' || data == true)
                    this.setState({
                        isLogined: true
                    })
            })
    }

    render() {
        return (
            this.state.isLogined ? <DashboardPageHeader /> : <LoginPageHeader />
        );
    }
}
