import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import { asyncLocalStorage } from '../../common/helper';
import { SERVER_URL, USER_DATA, LOGGED_IN_TIME, IS_LOGGED_IN, TOKEN_DATA } from '../../common/Constant'
import './header.css';
class DasbhboardPageHeader extends Component {
    constructor(props) {
        super(props);
        this.state = {
            width: 0,
            height: 0,
            isloginState: false,
            isProviderClick: false,
            isLogedIn: false,
            customerFirstName: '',
            customerLastName: '',
            customerMiddlename: '',
            customerProfilePic: '',
            loggedInTime: ''
        };
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }

    componentDidMount() {
        console.log(this.state.isloginState);
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
        asyncLocalStorage.getItem(LOGGED_IN_TIME)
            .then(data => {
                this.setState({
                    loggedInTime: data
                });
            })

        asyncLocalStorage.getItem(USER_DATA)
            .then(data => {
                let jsonData = JSON.parse(data)
                if (Object.keys(jsonData).length > 0) {
                    console.log("object", jsonData)
                    this.setState({
                        // loggedInTime,
                        isLogedIn: true,
                        customerFirstName: jsonData.firstName,
                        customerLastName: jsonData.lastName,
                        customerMiddlename: jsonData.middleName,
                        customerProfilePic: jsonData.profileimage != undefined ? SERVER_URL + 'user/image/' + jsonData.profileimage : '',
                    });
                }
            })
    }
    componentWillMount() {
        if (localStorage.getItem('token') !== '') {
            this.setState({ isloginState: true });
        } else {
            this.setState({ isloginState: false });
        }
    }
    logoutHandler = async () => {
        console.log('called here');
        await asyncLocalStorage.setItem('token', '')
        await asyncLocalStorage.setItem(USER_DATA, "{}")
        await asyncLocalStorage.setItem(IS_LOGGED_IN, false)
        this.props.history.push('/login')
    };

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }
    toggleMenu() {
        // console.log(this.state.width, this.state.height);
        if (window.innerWidth < 991) {
            console.log(
                document
                    .getElementById('navbarSupportedContent')
                    .className.search('show')
            );
            if (
                document
                    .getElementById('navbarSupportedContent')
                    .className.search('show') < 0
            ) {
                document.getElementById('navbarSupportedContent').className =
                    document.getElementById('navbarSupportedContent').className + ' show';
            } else {
                document.getElementById('navbarSupportedContent').className =
                    'collapse navbar-collapse';
            }
        }
    }

    render() {

        return (
            <React.Fragment>
                <header
                    className="loginheader HJ_search"
                >
                    <nav className="navbar navbar-expand-lg navbar-dark bg-light">
                        <Link to="/home" className="navbar-brand">
                            <img
                                src={
                                    '/images/header_logo2.png'
                                }
                            />
                        </Link>
                        <button
                            className="navbar-toggler"
                            type="button"
                            data-toggle="collapse"
                            data-target="#navbarSupportedContent"
                            aria-controls="navbarSupportedContent"
                            aria-expanded="false"
                            aria-label="Toggle navigation"
                            onClick={this.toggleMenu}
                        >
                            <span className="navbar-toggler-icon" />
                        </button>
                        <div
                            className="collapse navbar-collapse"
                            id="navbarSupportedContent">

                            <ul className="navbar-nav mr-auto">
                                <li className="nav-item dropdown">
                                    <a
                                        className="nav-link dropdown-toggle"
                                        href="#"
                                        id="navbarDropdown"
                                        role="button"
                                        data-toggle="dropdown"
                                        aria-haspopup="true"
                                        aria-expanded="false"
                                    >
                                        Resource Center
                      </a>
                                    <div
                                        className="dropdown-menu"
                                        aria-labelledby="navbarDropdown"
                                    >
                                        <a className="dropdown-item" href="#">
                                            Action
                        </a>
                                        <a className="dropdown-item" href="#">
                                            Another action
                        </a>
                                        <a className="dropdown-item" href="#">
                                            Something else here
                        </a>
                                    </div>
                                </li>
                                <li className="nav-item">
                                    <Link to="/faq" className="nav-link">
                                        FAQs
                      </Link>
                                </li>
                                <li className="nav-item">
                                    <Link to="/support" className="nav-link">
                                        Support
                      </Link>
                                </li>
                                <li className="nav-item dropdown notification">
                                    <Link className="nav-link dropdown-toggle active" to="">
                                        <i className="fas fa-bell">
                                            {' '}
                                            <span className="count-msg">3</span>{' '}
                                        </i>
                                    </Link>
                                    <div className="dropdown-menu">
                                        <ul>
                                            <li>
                                                <a href="#" className="unread">
                                                    message one{' '}
                                                    <small className="timespapm"> 4 hrs ago</small>
                                                </a>
                                            </li>
                                            <li>
                                                <a href="#" className="unread">
                                                    message two{' '}
                                                    <small className="timespapm"> 1 week ago</small>
                                                </a>
                                            </li>
                                            <li>
                                                <a href="#" className="unread">
                                                    message two{' '}
                                                    <small className="timespapm"> 12/02/2019</small>
                                                </a>
                                            </li>
                                            <li>
                                                <a href="#" className="read">
                                                    message two{' '}
                                                    <small className="timespapm"> 12/02/2019</small>
                                                </a>
                                            </li>
                                            <li>
                                                <a href="#" className="read">
                                                    message two{' '}
                                                    <small className="timespapm"> 12/02/2019</small>
                                                </a>
                                            </li>
                                            <li>
                                                <a href="#" className="read">
                                                    message two{' '}
                                                    <small className="timespapm"> 12/02/2019</small>
                                                </a>
                                            </li>
                                        </ul>
                                    </div>
                                </li>
                            </ul>


                            <form className="form-inline my-2 my-lg-0 dropdown">
                                <div className="userlogin">
                                    <Link
                                        className="active dropdown-toggle"
                                        to="admin/provider"
                                        id="navbarDropdown"
                                        role="button"
                                        data-toggle="dropdown"
                                        aria-haspopup="true"
                                        aria-expanded="false"
                                    >
                                        <div className="media">
                                            <figure className="img-rounded media-left">
                                                <img src={this.state.customerProfilePic != '' ? this.state.customerProfilePic : '/images/client1.png'} />
                                            </figure>
                                            <div className="media-body">
                                                <h6 className="active">{this.state.customerFirstName} {this.state.customerMiddlename} {this.state.customerLastName}</h6>
                                                <small>Last Login on {this.state.loggedInTime}</small>
                                            </div>
                                        </div>
                                    </Link>
                                    <div
                                        className="dropdown-menu"
                                        aria-labelledby="navbarDropdown"
                                    >
                                        <Link className="dropdown-item" to="/provider">
                                            Profile
                        </Link>
                                        <Link className="dropdown-item" to="/setting">
                                            Setting
                        </Link>
                                        <Link
                                            className="dropdown-item"
                                            to="/login"
                                            onClick={this.logoutHandler}
                                        >
                                            Logout
                        </Link>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </nav>
                </header>
            </React.Fragment>
        );
    }
}

export default withRouter(DasbhboardPageHeader);
