import React, { Component } from 'react';
import { Map, InfoWindow, Marker, GoogleApiWrapper } from 'google-maps-react';
import { Post } from '../../Sevices/Post';

import Sidebar from '../../component/sidebar/sidebar';
import Slider, { createSliderWithTooltip } from 'rc-slider';
import Loader from 'react-loader-spinner';
import { withRouter } from 'react-router';
import 'rc-slider/assets/index.css';
import './search.css';
import Alert from '../../common/Alert'
import ProviderSingleItem from './ProviderSingleItem';
import InfoWindowEx from './InfoWindowEx';
import Header from '../../component/header';


class SearchPage extends Component {
  constructor(props) {
    super(props);
    // Don't call this.setState() here!
    this.state = {
      posts: {},
      userMsg: {},
      mapView: {},
      listView: {},
      isLoading: true,
      filters: [],
      startDate: new Date(),
      viewData: 'list',
      // latitude:28.6115974,
      // longitude:77.3813075
      latitude: 28.6115974,
      longitude: 77.3813075,
      sliderValue: 6,
      searchResults: null,
      searchResponses: null,
      showingInfoWindow: true,
      activeMarker: {},
      selectedPlace: {},
      providerList: [],
      loading: false,
      mapData: [],
      display: 'none',
      error: false,
      message: ""
    };
    this.viewList = this.viewList.bind(this); // make a estance of searchPage class  we have to bind function and insert intself
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    var data = {
      distance: 1000,
      latlong: [this.state.longitude, this.state.latitude],
      filter_type: 'list'
    };
    var item = { ...data };
    this.getListViewDetails(item);
    var data_map = {
      distance: parseInt(this.state.sliderValue) * 1609.344,
      latlong: [this.state.longitude, this.state.latitude],
      filter_type: 'map'
    };
    this.getMapViewDetails(data_map);
  }

  viewList = val => {
    if (val == 'map') {
      this.setState({
        viewData: 'map'
      });
    } else {
      this.setState({
        viewData: 'list'
      });
    }
  };

  onMarkerClick = (props, marker, e) => {
    this.setState(
      {
        selectedPlace: props.place_,
        activeMarker: marker,
        showingInfoWindow: true
      },
      () => console.log('Marker clicked')
    );
  };

  onMapClicked = props => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      });
    }
  };

  handleChange = sliderValues => {
    this.setState({ sliderValues });
  };
  //   percentFormatter = (value) => {
  //     return value
  //   }

  onAfterSliderChange = value => {
    this.setState(
      {
        sliderValue: value
      },
      () => {
        var data = {
          distance: parseInt(this.state.sliderValue) * 1609.344,
          latlong: [this.state.longitude, this.state.latitude],
          filter_type: 'map'
        };
        this.getMapViewDetails(data);
      }
    );
  };
  onSliderChange = value => {
    this.setState(
      {
        sliderValue: value
      }
      // , () => {
      //     var data = {
      //         distance: parseInt(this.state.sliderValue) * 1609.344,
      //         latlong: [this.state.longitude, this.state.latitude],
      //         filter_type: "map"
      //     }
      //     this.getMapViewDetails(data)
      // }
    );
  };

  getMyLocation = () => {
    console.log('getMyLocation');
    const location = window.navigator && window.navigator.geolocation;
    console.log('location is', location);
    if (location) {
      console.log('inside if');
      location.getCurrentPosition(
        position => {
          this.setState(
            {
              latitude: position.coords.latitude,
              longitude: position.coords.longitude
            },
            () => console.log('lat in callback is ', this.state.latitude)
          );
        },
        error => {
          console.log('Setting lat long to error strings');
          this.setState({
            latitude: 'err-latitude',
            longitude: 'err-longitude'
          });
        },
        { enableHighAccuracy: false, timeout: 50000 }
      );
    }
  };

  getListViewDetails = data => {
    var item = data;

    Post('provider/filter', item).then(result => {
      let responseJSON = result;

      if (responseJSON !== undefined && responseJSON.data) {
        this.setState({
          providerList: Array.isArray(responseJSON.data)
            ? responseJSON.data
            : [],
          userMsg: responseJSON,
          searchResponses: responseJSON.data.length
        });
        console.log('list View Details are here', this.state.userMsg);
      } else {
        alert('error encountered');
      }
    });
  };

  getMapViewDetails = user => {
    console.log('lat is ', this.state.latitude);
    // var user = {
    //     distance: parseInt(this.state.sliderValue)*1609.344,
    //     latlong: [this.state.longitude,this.state.latitude],
    //     filter_type: "map"
    // }

    Post('provider/filter', user).then(result => {
      let responseJSON = result;

      if (responseJSON !== undefined && responseJSON.data) {
        this.setState({
          mapView: responseJSON,
          searchResults: responseJSON.data.length,
          mapData: responseJSON.data
        });
        console.log('map View Details are here', this.state.mapView);
      } else {
        alert('Some error encountered');
      }
    });
  };
  filterdata = data => {
    let default_value = {
      distance: 1000,
      latlong: [this.state.longitude, this.state.latitude],
      filter_type: 'list'
    };
    let item = { ...default_value, ...data };
    this.getListViewDetails(item);
    let default_value_map = {
      distance: parseInt(this.state.sliderValue) * 1609.344,
      latlong: [this.state.longitude, this.state.latitude],
      filter_type: 'map'
    };
    let item_map = { ...default_value_map, ...data };
    this.getMapViewDetails(item_map);
  };

  componentWillMount() {
    console.log('componentWillMount');
    this.getMyLocation();
    //  this.getDetails()
  }

  printdetails = () => {
    console.log('i am clicked here');
  };

  handleFilter = e => {
    // debugger
    var filter_value = e.currentTarget.dataset.id;
    var clone = this.state.filters;
    var mutable_copy_filter = clone.slice(0);
    if (mutable_copy_filter.includes(filter_value)) {
      var position = mutable_copy_filter.indexOf(filter_value);
      mutable_copy_filter.splice(position, 1);
      this.setState({ filters: mutable_copy_filter });
    } else {
      mutable_copy_filter.push(filter_value);

      this.setState({ filters: mutable_copy_filter });
    }
  };

  handleChange(date) {
    this.setState({
      startDate: date
    });
  }
  toggleSidebar = () => {
    var sdcla = document.getElementById('sidebar').className;
    if (sdcla === 'sidebar') {
      document.getElementById('sidebar').className = sdcla + ' active';
      document.getElementById('wrapperIdContent').className =
        document.getElementById('wrapperIdContent').className + ' active';
    } else {
      document.getElementById('sidebar').className = 'sidebar';
      document.getElementById('wrapperIdContent').className = 'inner-wrapper';
    }
  };

  showToast = () => {
    this.setState({
      display: 'block',
      error: true,
      message: "You need to login"
    });
    setTimeout(() => {
      this.setState({
        display: 'none',
        error: true,
        message: "You need to login"
      });
    }, 1000)
  }

  render() {
    return (
      <div>
        <Alert
          display={this.state.display}
          error={this.state.error}
          message={this.state.message}
        />
        <Header />


        <div className="search-page">
          <article className="top-search-panel">
            <div className="container-fluid">
              <div>
                <div className="row">
                  <div className="col-md-4 col-sm-6">
                    <ul className="breadcum">
                      <li>
                        <a href="#">Arizona</a>
                      </li>
                      <li>
                        <a href="#">Advanced Search</a>
                      </li>
                    </ul>
                  </div>
                  <div className="col-md-4 col-sm-6 text-center">
                    {this.state.viewData == 'map' ? (
                      <div className="paggination">
                        {this.state.searchResults} Results | Radius{' '}
                        <span className="rangeSlider">
                          <Slider
                            min={6}
                            max={62}
                            value={this.state.sliderValue}
                            onAfterChange={this.onAfterSliderChange}
                            onChange={this.onSliderChange}
                            disabled={
                              this.state.latitude !== 'err-latitude' &&
                                this.state.longitude !== 'err-longitude'
                                ? false
                                : true
                            }
                          />
                        </span>
                        &nbsp; {this.state.sliderValue} Miles
                      </div>
                    ) : (
                        <div className="paggination">
                          {this.state.searchResponses} Results{' '}
                          <img src={'/images/prev_arrow.png'} /> Page 1 of 12{' '}
                          <img src={'/images/next-arrow.png'} />
                        </div>
                      )}
                  </div>
                  <div className="col-md-4 text-right">
                    <ul className="nav nav-option">
                      <li
                        className={
                          this.state.viewData == 'map'
                            ? 'nav-link active'
                            : 'nav-link'
                        }
                      >
                        {
                          <span
                            href="#"
                            onClick={() => {
                              this.viewList('map');
                            }}
                          >
                            {' '}
                            <img src={'/images/map_view.png'} /> &nbsp; Map View
                          </span>
                        }
                      </li>
                      <li
                        className={
                          this.state.viewData == 'list'
                            ? 'nav-link active'
                            : 'nav-link'
                        }
                      >
                        {
                          <span
                            href="#"
                            onClick={() => {
                              this.viewList('list');
                            }}
                          >
                            {' '}
                            <i className="fas fa-list" /> &nbsp; List View
                          </span>
                        }
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </article>
          {this.state.viewData == 'map' ? (
            <article className="mapSearch">
              <div className="Address">Address</div>
              <div className="form-group">
                <input type="text" className="form-control" />{' '}
                <span>
                  <img src={'/images/gmap_logo.png'} />
                </span>{' '}
              </div>
            </article>
          ) : (
              ''
            )}
          <Sidebar filterdata={this.filterdata} />
          <div className="inner-wrapper" id="wrapperIdContent">
            <a href="#" className="setting" onClick={this.toggleSidebar}>
              <i className="fas fa-cog" />
            </a>
            {this.state.viewData == 'list' ? (
              <section>
                <div className="search-list">
                  {this.state.loading ? (
                    <Loader
                      type="Puff"
                      color="#00BFFF"
                      height="100"
                      width="100"
                    />
                  ) : (
                      this.state.providerList.map((item, key) => {
                        return (
                          <ProviderSingleItem
                            key={key}
                            data={item}
                            history={this.props.history}
                          />
                        );
                      })
                    )}
                </div>
                <nav aria-label="...">
                  <ul className="pagination pagination-sm">
                    <li className="page-item active">
                      <a className="page-link" href="#">
                        1
                      </a>
                    </li>
                    <li className="page-item">
                      <a className="page-link" href="#">
                        2
                      </a>
                    </li>
                    <li className="page-item">
                      <a className="page-link" href="#">
                        3
                      </a>
                    </li>
                  </ul>
                </nav>
              </section>
            ) : (
                //Map view begins here
                <div className="map-marker-container">
                  {this.state.latitude !== 'err-latitude' &&
                    this.state.longitude !== 'err-longitude' ? (
                      <Map
                        google={this.props.google}
                        style={{
                          width: '100%',
                          height: '100%',
                          position: 'relative'
                        }}
                        className={'map'}
                        zoom={14}
                        initialCenter={{
                          lat: this.state.latitude,
                          lng: this.state.longitude
                        }}
                      >
                        {this.state.mapData.map((data, i) => {
                          const lat = data.address[0].location[1];
                          const long = data.address[0].location[0];
                          //console.log("lat long are ",lat,long)
                          return (
                            <Marker
                              //name={`${data.firstName} ${data.middleName} ${data.lastName}`}
                              position={{ lat: lat, lng: long }}
                              place_={data}
                              key={i}
                              // place_={place}
                              onClick={this.onMarkerClick}
                              icon={{
                                url: '/images/therapist.png',
                                height: '40px',
                                width: '40px'
                              }}
                            />
                          );
                        })}
                        <InfoWindowEx
                          marker={this.state.activeMarker}
                          visible={this.state.showingInfoWindow}
                        >
                          <div>
                            {this.state.mapView.data.map((item, i) => {
                              let streetAddress = item.address[0].streetaddress;
                              let locality = item.address[0].locality;
                              let country = item.address[0].country;
                              let zip_code = item.address[0].zip_code;
                              let formatted = item.address[0].formatted;
                              let id = item._id;
                              let firstName = item.firstName;
                              let lastName = item.lastName;
                              let middleName = item.middleName;
                              let region = item.address[0].region;
                              let phone = item.phone[0];
                              return (
                                <div className="appointment_bx" key={i}>
                                  <div className="dflex">
                                    <div className="left_side">
                                      <h3>
                                        {item.firstName} {item.middleName}{' '}
                                        {item.lastName}
                                      </h3>
                                      <small>Counselor/Therapist</small>
                                      <img src="/images/star.png" />
                                      <a>{item.phone[0]}</a>
                                      <address>
                                        {streetAddress} <br />
                                        {formatted} <br />
                                        {locality}, {country} {zip_code}
                                      </address>
                                    </div>

                                    <div
                                      className="right_side"
                                      style={{ margin: 0 }}
                                    >
                                      <div className="user_img">
                                        <img
                                          src="/images/client1.png"
                                          style={{
                                            borderRadius: '50%',
                                            marginTop: -70
                                          }}
                                        />
                                        <a href="#">
                                          <img src="/images/green_check_icon.png" />
                                        </a>
                                      </div>
                                      <div class="btn-wrapper">
                                        <button
                                          class="btn btn-success mb-2"
                                          onClick={() =>
                                            this.props.history.push({
                                              pathname: '/therapist',
                                              //search: '?query=abc',
                                              state: { userid: id }
                                            })
                                          }
                                        >
                                          {' '}
                                          <i class="fas fa-search" />
                                          View Profile{' '}
                                        </button>
                                        <button
                                          type="button"
                                          class="btn btn-primary"
                                          onClick={async () =>
                                            {
                                              this.props.history.push({
                                                pathname: '/appointment',
                                                //search: '?query=abc',
                                                state: {
                                                  userid: id,
                                                  firstName: firstName,
                                                  middleName: middleName,
                                                  lastName: lastName,
                                                  phone: phone,
                                                  streetaddress: streetAddress,
                                                  locality: locality,
                                                  region: region,
                                                  country: country,
                                                  zip_code: zip_code
                                                }
                                              })
                                            }
                                          }
                                        >
                                      {' '}
                                      Book An Appointment here{' '}
                                    </button>
                                      </div>
                                    </div>
                                  </div>
                                  <div className="ftr-text">
                                    <div class="app">
                                      <label>
                                        Next Available New Patient Appointment
                                  </label>
                                      <p>
                                        <span>
                                          <i class="fas fa-calendar" />
                                          &nbsp;October 12, 2018
                                    </span>
                                        <span>
                                          <i class="fas fa-clock" />
                                          &nbsp;10:00 am
                                    </span>
                                      </p>
                                    </div>
                                  </div>
                                </div>
                              );
                            })}
                          </div>
                        </InfoWindowEx>

                        {/* <div>
                                                <input type="text" value={this.state.latitude} />
                                                <input type="text" value={this.state.longitude} />
                                                    </div>  */}

                        {/* <InfoWindow
                                                marker={this.state.activeMarker}
                                                >
                                                
                                                </InfoWindow> */}
                      </Map>
                    ) : (
                      <p>Please grant location permission to access Map View</p>
                    )}
                </div>
              )}
          </div>
        </div>
      </div>
    );
  }
}

