import * as React from 'react';
import {
    HashRouter,
    BrowserRouter,
    Route,
    Switch,
    Redirect
} from 'react-router-dom';
import ROUTES from './routes';
import Layout from './component/index';
import errorPage from './screens/404page/errorPage';
import Login from './common/login/login'
import CustomerProfile from './screens/customer/pdprofile'
import Appointment from './screens/appointment/appointment'
import Therapist from './screens/therapist/therapist'
// import Auth from './Auth'



class RoutesComp extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            isLogin: false,
            token: " ",
            stars: ""
        }

    }
    sendStars = (params) => {
        this.setState({
            stars: params
        })
    }
    // tokenhandler = (token) =>{
    //     console.log("token value is",token)
    //     this.setState({token:token})
    //     console.log("token state",this.state.token)
    // }
    IsLoginHandler = () => {
        console.log("login method method is called")
        this.setState({ isLogin: true })
    }

    IsLogoutHandler = () => {
        console.log("logout method method is called")
        this.setState({ isLogin: false })
    }


    IsLogoutHandler = () => {
        console.log('logout method method is called');
        this.setState({ isLogin: false });
    };

    render() {
        return (
            <BrowserRouter basename={'/'} >
                <Layout isLogin={this.state.isLogin} >
                    <Switch>
                        <Route path={ROUTES.LOGIN.url}
                            render={props => <Login IsLoginHandler={this.IsLoginHandler}

                            />}
                        />
                        <Route path={ROUTES.HOME.url} component={ROUTES.HOME.comp} />
                        <Route path={ROUTES.ABOUT.url} component={ROUTES.ABOUT.comp} />
                        <Route path={ROUTES.CONTACT.url} component={ROUTES.CONTACT.comp} />
                        <Route path={ROUTES.FAQ.url} component={ROUTES.FAQ.comp} />
                        <Route path={ROUTES.WORK.url} component={ROUTES.WORK.comp} />
                        <Route path={ROUTES.SEARCH.url} component={ROUTES.SEARCH.comp} />
                        <Route path={ROUTES.BLOG.url} component={ROUTES.BLOG.comp} />

                        <Route path="/provider/:id"
                            component={ROUTES.THERAPIST.comp}
                        />
                        {/* <Auth> */}
                        {/* <Route path={ROUTES.APPOINTMENT.url}
                            render={props => <Appointment stars={this.state.stars} />}
                        /> */}
                        <Route path="/appointment/:id"
                            render={props => <Appointment stars={this.state.stars}

                            />}
                        />
                        <Route path={ROUTES.PDPROFILE.url} render={props => <CustomerProfile IsLogoutHandler={this.IsLogoutHandler} />} />
                        <Route path={ROUTES.PROFILESETTING.url} component={ROUTES.PROFILESETTING.comp} />
                        <Route path={ROUTES.COMFIRMPASSWORD.url} component={ROUTES.COMFIRMPASSWORD.comp} />

                        <Route path={ROUTES.RESET.url} component={ROUTES.RESET.comp} />
                        {/* </Auth> */}




                        <Route path={ROUTES.BLOGDETAIL.url} component={ROUTES.BLOGDETAIL.comp} />
                        <Route path={ROUTES.THERAPYTYPE.url} component={ROUTES.THERAPYTYPE.comp}
                        // render={props => <Therapist sendStars={this.sendStars} 

                        // />}
                        />
                        <Route path={ROUTES.HOTLINES.url} component={ROUTES.HOTLINES.comp} />
                        <Route path={ROUTES.SUPPORT.url} component={ROUTES.SUPPORT.comp} />
                        <Route path={ROUTES.PROVIDERLANDINGPAGE.url} component={ROUTES.PROVIDERLANDINGPAGE.comp} />

                        <Route exact path={'/'} render={() => (<Redirect to="/home" />)} />
                        <Route path={'**'} component={errorPage} />
                        <Redirect to="/" />
                    </Switch>
                </Layout>
            </BrowserRouter>
        );
    }
};

export default RoutesComp;
