import React, { Component } from 'react';
import { GetAuth } from '../../Sevices/GetAuth'
import { PostAuth } from '../../Sevices/PostAuth'
import { PatchAuth } from '../../Sevices/PatchAuth'
import AddFamily from './AddFamily'

class Provider_family extends Component {
    constructor(props) {
        super(props)
        this.state = {
            firstName: "",
            lastName: "",
            relationship: "",
            dob: "",
            attributeForm: [],
            data1: "",
            data2: "",
            arraydata: [],
            listarraystate: [],
            getMembersData: {}
        }
        //this.addAttributeForm();
    }

    onChangeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }
    arraydataHandler = (userdata) => {

        console.log("userdata", userdata)
        // var listarray = this.state.listarraystate
        // listarray.push(userdata)
        // console.log("listarray contents are",listarray)
        // this.setState({listarraystate:listarray},() => {
        //     console.log("data in the callback after setstate",this.state.listarraystate)

        // } )
        PostAuth('user/familymamber', userdata).then((result) => {
            let responseJSON = result;
            console.log("Response is here", responseJSON)
            if (responseJSON) {
                console.log("msg are here", responseJSON.message)
            } else {
                alert("Enter valid credentials")
            }

        }).then(() => {
            this.getMembersDataHandler()
        })

    }


    onChange = date => this.setState({ date })

    onChangeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })

    }

    addAttributeForm = (e) => {
        //this method adds label and input field to the state array 
        e.preventDefault()
        var array = this.state.attributeForm;
        array.push(<AddFamily removeElementHandler={this.removeElementHandler} arraydataHandler={this.arraydataHandler} />);
        this.setState({ attributeForm: array });
        console.log("items of the array are", array)
    }

    removeElementHandler = (e) => {
        e.preventDefault()
        var array = this.state.attributeForm
        var position = array.length - 2
        array.splice(position, 1)
        this.setState({ attributeForm: array })
        console.log("wjwbhjwbukjb", array)
    }



    componentDidMount() {
        this.getMembersDataHandler()
    }

    getMembersDataHandler = () => {
        GetAuth(`user/getfamilymambers`).then((result) => {
            let responseJSON = result;
            console.log("-=-=-=-=-=-=-=-=", responseJSON)
            if (responseJSON) {
                this.setState({ getMembersData: responseJSON.data })
                console.log("------->details are here", this.state.getMembersData)
            }
            else {
                alert("No response")
            }

        })
    }
    removeMembers = (id, e) => {
        e.preventDefault()
        PatchAuth(`user/removefamilymamber/${id}`).then((result) => {
            let responseJSON = result;
            console.log("Response is here", responseJSON)
            if (responseJSON) {
                console.log("msg are here", responseJSON.message)
            } else {
                alert("Some error ")
            }

        })
            // e.preventDefault()
            .then(() => {
                this.getMembersDataHandler()
            })

    }




    render() {
        return (
            <div className="family-setting">
                <form>
                    <div className="row">
                        <div className="col-sm-12 text-left presion-list">
                            {this.state.getMembersData.relation != null ?
                                this.state.getMembersData.relation.map((data, i) => {
                                    return (
                                        <span class="badge badge-default badge-lg">{data.firstName} {data.lastName} ({data.relationship}) <button onClick={this.removeMembers.bind(this, data._id)}>x</button> </span>
                                    )
                                }) :
                                null

                            }
                            {/* <span class="badge badge-default badge-lg">John Deo (Cusion) <a href="#">x</a> </span> */}
                        </div>



                        <div>
                            {this.state.attributeForm.map(input => {
                                return input
                            })
                            }
                            <div className="col-sm-12 text-left">
                                <button className="btn btn-primary" onClick={(e) => this.addAttributeForm(e)}><i className="fa fa-plus"></i> Add Another Person</button>
                            </div>
                        </div>
                    </div>
                </form>
            </div >
        );
    }
}

export default Provider_family;



