import React, { Component } from 'react'
import moment from 'moment'
import cx from 'classnames';
import { PostAuth } from '../../Sevices/PostAuth'
import { GetAuth } from '../../Sevices/GetAuth'
import { Calendar } from 'react-calendar-component';
import 'moment/locale/nb';
import './Calendar.css';

export default class CalendarView extends Component {
    constructor(props) {
        super(props)
        this.state = {
            selectedDate: "",
            viewAppointments: {},
            date: moment(),
            filteredViewState: false,
            filteredData: "",
            markedDates: []
        }
    }
    callApiHandler(date) {
        var d = moment(date)
        var month = d.month() + 1
        var year = d.year()
        console.log("month is", month)
        console.log("year is ", year)

        this.setState({ filteredViewState: false })
        var user = {
            month: month,
            year: year
        }

        PostAuth('appointment/upcomingappointment', user).then((result) => {
            let responseJSON = result;
            console.log("-=-=-=-=-=-=-=-=", responseJSON)
            if (responseJSON) {
                this.setState({ viewAppointments: responseJSON.data, })
                console.log("calender view details are here", this.state.viewAppointments)
            }
            else {
                alert("Enter valid credentials")
            }

        })

    }
    componentDidMount() {
        this.callApiHandler()
    }

    filteredDataHandler = () => {
        this.setState({ filteredViewState: true })
        var filter = this.state.viewAppointments.filter(item => moment(item.appointment_date, 'YYYY-MM-DD').isSame(moment(this.state.selectedDate, 'YYYY-MM-DD')))
        this.setState({ filteredData: filter })
        console.log("filtered data is", filter)
    }



    getDate = (date) => {
        var d = moment(date)
        var datevalue = d.format('YYYY-MM-DD')
        this.setState({ selectedDate: datevalue }, () => {
            this.filteredDataHandler()
        })
        console.log("date is here", datevalue)
    }

    render() {
        // let filteredData = this.state.selectedDate == '' ? this.state.viewAppointments :
        //console.log("filterred data is ",this.state.viewAppointments.filter(item => moment(item.appointment_date, 'YYYY-MM-DD').isSame(moment(this.state.selectedDate, 'YYYY-MM-DD'))))
        // console.log("render check",this.state.monthNum)
        // console.log("render year",this.state.displayYear)
        return (
            <div className="card calendar-view">
                <div className="row">
                    <div className="col-sm-6">
                        <div className="card">

                            <Calendar
                                onChangeMonth={date => this.setState({ date }, () => { this.callApiHandler(date._d) })}
                                date={this.state.date}
                                // onPickDate={date => console.log(date)}
                                onPickDate={date => this.getDate(date._d)}
                                renderDay={({ day, classNames, onPickDate }) => (
                                    <div
                                        key={day.format()}
                                        className={cx(
                                            'Calendar-grid-item',
                                            day.isSame(moment(), 'day') && 'Calendar-grid-item--current',
                                            classNames
                                        )}
                                        onClick={e => onPickDate(day)}>
                                        {day.format('D')}
                                    </div>
                                )}
                                renderHeader={({ date, onPrevMonth, onNextMonth }) => (
                                    <div className="Calendar-header">
                                        <button onClick={onPrevMonth}></button>
                                        <div className="Calendar-header-currentDate">
                                            {date.format('MMMM YYYY')}
                                        </div>
                                        <button onClick={onNextMonth}></button>
                                    </div>
                                )}
                            />
                        </div>
                    </div>
                    <div className="col-sm-6">
                        <ul>
                            {this.state.filteredViewState === false ?
                                this.state.viewAppointments.length >= 1 ? this.state.viewAppointments.map((data, i) => {
                                    return (
                                        <li key={i} data-id={data._id}>

                                            <div className="appoint-card" id="appointment">
                                                <div className="row">
                                                    <div className="col-sm-12">
                                                        <div className="media">
                                                            <span className="time media-left  media-middle">
                                                                <label>{moment(data.appointment_date).format('dddd')}</label>
                                                                <h1>{moment(data.appointment_date, "YYYY-MM-DD").date()}</h1>
                                                                <label>{moment(data.appointment_date).format('MMMM')}</label>
                                                            </span>
                                                            <div className="media-body text-left media-middle">
                                                                <h5 className="text-primary">{data.providerId.firstName} {data.providerId.lastName}</h5>
                                                                <p>New patient Discovery</p>
                                                                <p>{data.appointment_location.address.name}</p>
                                                                <i className="text-success">{data.scheduled_start_time} - {data.scheduled_end_time}</i>
                                                            </div>
                                                            <div className="media-right">
                                                                <a href="#"><i className="text-success">$225</i>&nbsp; <i className="fa fa-angle-right text-default"></i></a>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                        </li>
                                    )
                                }
                                )
                                    : <p>No Upcoming Appointments found</p>
                                :
                                this.state.filteredData.length >= 1 ? this.state.filteredData.map((data, i) => {
                                    return (
                                        <li key={i} data-id={data._id}>

                                            <div className="appoint-card" id="appointment">
                                                <div className="row">
                                                    <div className="col-sm-12">
                                                        <div className="media">
                                                            <span className="time media-left  media-middle">
                                                                <label>{moment(data.appointment_date).format('dddd')}</label>
                                                                <h1>{moment(data.appointment_date, "YYYY-MM-DD").date()}</h1>
                                                                <label>{moment(data.appointment_date).format('MMMM')}</label>
                                                            </span>
                                                            <div className="media-body text-left media-middle">
                                                                <h5 className="text-primary">{data.providerId.firstName} {data.providerId.lastName}</h5>
                                                                <p>New patient Discovery</p>
                                                                <p>{data.appointment_location.address.name}</p>
                                                                <i className="text-success">{data.scheduled_start_time} - {data.scheduled_end_time}</i>
                                                            </div>
                                                            <div className="media-right">
                                                                <a href="#"><i className="text-success">$225</i>&nbsp; <i className="fa fa-angle-right text-default"></i></a>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>

                                        </li>
                                    )
                                }
                                ) :
                                    <p>No Upcoming Appointments found</p>

                            }
                        </ul>
                    </div>
                </div>
            </div>





        )




    }
}