import React, { Component } from 'react';
import './Modal.css';

class Modal extends React.Component {
    render(){
    console.log('modal props', this.props)
    const { handleClose, show, children } = this.props;
    const showHideClassname = show ? "Vd-modal modalOne display-block" : "modalOne display-none";
  
    return (
      <div className={showHideClassname}>
        <section className="modalOne-main">
          {children}
          <button className="Close_Popup" onClick={handleClose}><img src="/images/cut.png" /></button>
        </section>
      </div>
    );
    }
    
  };

export default Modal;
