import React, { Component } from 'react';
import SearchTop from '../../common/top-search-panel/searchPanel';
import BannerTop from '../../common/top-banner/banner_top';
import AppBanner from '../../common/app-banner/appBanner';
import { Link } from 'react-router-dom';
import './therapyType.css';

class TherapyType extends Component {
    state = {
        bannerTitle: {
            title: 'Types of Therapy',
            subTitle: 'HOPJAX RESOURCE CENTER',
            img: 'type_therapy_bg.png'
        }
    }
    render() {
        return (
            <div className="type-therapy">
                <SearchTop />
                <BannerTop bannerData={this.state.bannerTitle} />
                <div className="breadcum text-center">
                    <ul><li><Link className="nav-links" to="">Resource Center</Link></li><li className="active" >Types of Therapy</li></ul>
                </div>
                <div className="therapy-content">
                    <div className="container">
                        <div className="intro-therapy">
                            <p>Mental health professionals use a variety of approaches to give people new tools to deal with ingrained, troublesome patterns of behavior and to help them manage symptoms of mental illness. The best therapists will work with you to determine a treatment plan that will be most effective for you. This sometimes involves a single method or it may involve elements of several different ones, often referred to as an "eclectic approach" to therapy.</p>
                            <p>Keep in mind that new research can yield rapid and dramatic changes in our understanding of, and approaches to, mental disorders.</p>
                            <h6 className="subtitle active">The following is a brief description of the methods mental health professionals most commonly use:</h6>
                        </div>
                        <hr></hr>
                        <div className="content">
                            <h3 className="active">Behavioral Therapy</h3>
                            <p>As the name implies, this approach focuses on behavior - changing unwanted behaviors through rewards, reinforcements, and desensitization. Desensitization is a process of confronting something that arouses anxiety, discomfort, or fear and overcoming the unwanted responses. Someone whose fear of germs leads to excessive washing, for example, may be trained to relax and not wash his or her hands after touching a public doorknob. Behavioral therapy often involves the cooperation of others, especially family and close friends, to reinforce a desired behavior.</p>
                        </div>
                        <hr></hr>
                        <div className="content">
                            <h3 className="active">Biomedical Treatment</h3>
                            <p>Medication alone, or in combination with psychotherapy, has proven to be an effective treatment for a number of emotional, behavioral, and mental disorders. The kind of medication a psychiatrist prescribes varies with the disorder and the individual being treated. For example, some people who suffer from anxiety, bipolar disorder, major depression, obsessive compulsive disorder, panic disorders, and schizophrenia find their symptoms improve dramatically through careful monitoring of appropriate medication.</p>
                        </div>
                        <hr></hr>
                        <div className="content">
                            <h3 className="active">Cognitive Therapy</h3>
                            <p>This method aims to identify and correct distorted thinking patterns that can lead to feelings and behaviors that may be troublesome, self-defeating, or even self-destructive. The goal is to replace such thinking with a more balanced view that, in turn, leads to more fulfilling and productive behavior. Consider the person who will not apply for a promotion on the assumption that it is beyond reach, for example. With cognitive therapy, the next time a promotion comes up that person might still initially think, "I won't get that position..." but then immediately add, "unless I show my boss what a good job I would do."</p>
                        </div>
                        <hr></hr>
                        <div className="content">
                            <h3 className="active">Cognitive-Behavioral Therapy</h3>
                            <p>A combination of cognitive and behavioral therapies, this approach helps people change negative thought patterns, beliefs, and behaviors so they can manage symptoms and enjoy more productive, less stressful lives.</p>
                        </div>
                        <hr></hr>
                        <div className="content">
                            <h3 className="active">Couples Counseling and Family Therapy</h3>
                            <p>These two similar approaches to therapy involve discussions and problem-solving sessions facilitated by a therapist - sometimes with the couple or entire family group, sometimes with individuals. Such therapy can help couples and family members improve their understanding of, and the way they respond to, one another. This type of therapy can resolve patterns of behavior that might lead to more severe mental illness. Family therapy may be very useful with children and adolescents who are experiencing problems.</p>
                            <p>Coping with serious mental illness is hard on marriages and families. Family therapy can help educate the individuals about the nature of the disorder and teach them skills to cope better with the effects of having a family member with a mental illness - such as how to deal with feelings of anger or guilt. In addition, family therapy can help members identify and reduce factors that may trigger or worsen the disorder.</p>
                        </div>
                    </div>
                </div>
                <AppBanner />
            </div>
        );
    }
}

export default TherapyType;