import React, { Component } from 'react';
import axios from 'axios';
import Login from '../../common/login/login';
import Loader from 'react-loader-spinner';
import './appointment.css';

class Step1 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      profiledata: {},
      token: '',
      showMoreLocations: false,
      officeAddress: [],
      primaryOfficeAddress: {},
      availableForOffice: false,
      availableForVirtual: false,
      availableForInHome: false,
      book_appointment_location: {},
      finalAppointmentPayload: {},
      distance: 0,
      loading: false
    };
  }

  componentDidMount() {
    window.scrollTo(0, 0);
    const provider_id = this.props.provider_id;

    const token = localStorage.getItem('token');
    this.setState({ token: token, loading: true });
    axios
      .get(
        `http://66.175.217.67:8443/api/appointment/locations/${provider_id}`,
        {
          headers: { 'Content-Type': 'application/json', authenticate: token }
        }
      )
      .then(res => {
        if (res != undefined && res.data != undefined && res.data.data) {
          this.props.showStepZillaNavigation(true);
          this.updateFields(res.data.data);
        }
      })
      .catch(error => {
        //on error
        console.log('error', error);
      });
  }

  updateFields = providerData => {
    let checkAddressAvailable =
      Array.isArray(providerData.address) && providerData.address.length > 0;
    let bookAppointMent = {};

    if (checkAddressAvailable) {
      bookAppointMent = {
        type: 'Office',
        address: providerData.address[0]
      };
    } else if (providerData.appoinment_locations.indexOf('In-Home') > -1) {
      bookAppointMent = {
        type: 'In-Home'
      };
    } else {
      bookAppointMent = {
        type: 'Virtual'
      };
    }
    this.setState(
      {
        loading: false,
        providerData: providerData,
        officeAddress: checkAddressAvailable ? providerData.address : [],
        primaryOfficeAddress: checkAddressAvailable
          ? providerData.address[0]
          : {},
        availableForOffice:
          checkAddressAvailable &&
          providerData.appoinment_locations.indexOf('Office') > -1
            ? true
            : false,
        availableForVirtual:
          providerData.appoinment_locations.indexOf('Virtual') > -1
            ? true
            : false,
        availableForInHome:
          providerData.appoinment_locations.indexOf('In-Home') > -1
            ? true
            : false,
        book_appointment_location: bookAppointMent
      },
      () => {
        this.props.getDataFromStep1({
          appointment_location: this.state.book_appointment_location
        });
      }
    );

    if (checkAddressAvailable) {
      // this.getCurrentLocation(providerData.address[0]);
    } else {
      this.setState({ loading: false });
    }
  };
  showMoreLocations = () => {
    this.setState({ showMoreLocations: true });
  };

  onLocationSelected = item => {
    this.setState(
      {
        primaryOfficeAddress: item,
        showMoreLocations: false,
        book_appointment_location: {
          type: 'Office',
          address: item
        }
      },
      () =>
        this.props.getDataFromStep1({
          appointment_location: this.state.book_appointment_location
        })
    );
  };
  selectLocations = locationTypes => {
    let bookAppointMent = {};
    if (locationTypes == 'O') {
      bookAppointMent = {
        ...this.state.book_appointment_location,
        type: 'Office'
      };
    } else if (locationTypes == 'V') {
      bookAppointMent = {
        type: 'Virtual'
      };
    } else {
      bookAppointMent = {
        type: 'In-Home'
      };
    }
    this.setState(
      {
        book_appointment_location: bookAppointMent
      },
      () => {
        if (!this.isEmpty(this.state.book_appointment_location)) {
          this.props.getDataFromStep1({
            appointment_location: this.state.book_appointment_location
          });
        }
      }
    );
  };

  isEmpty = obj => {
    for (var key in obj) {
      if (obj.hasOwnProperty(key)) return false;
    }
    return true;
  };

  render() {
    return (
      <div>
        {this.state.token === '' ? (
          <Login
            loginFromAppointment={true}
            loginFromMapView={true}
            {...this.props}
          />
        ) : this.state.loading ? (
          <div className="loader_circle">
            <Loader type="Puff" color="#00BFFF" height="100" width="100" />
          </div>
        ) : (
          <div className="text-center step1">
            <h2>Book your appointment in under 2 minutes!</h2>
            <hr />
            <h5>What type of appointment would you like to book?</h5>
            <ul className="type-of-appointment">
              {this.state.availableForOffice &&
              !this.isEmpty(this.state.primaryOfficeAddress) ? (
                <li onClick={() => this.selectLocations('O')}>
                  <figure
                    style={
                      this.state.book_appointment_location.type === 'Office'
                        ? { borderColor: '#0b9f8d' }
                        : { borderColor: '#fff' }
                    }
                  >
                    <span>
                      <i className="far fa-building" />
                    </span>
                    <p>Office</p>
                  </figure>
                  <figcaption>
                    <p onClick={this.showMoreLocations}>
                      <a>{this.state.primaryOfficeAddress.country}</a>&nbsp;
                      <i className="fas fa-angle-down" />
                    </p>
                    <p>
                      {this.state.primaryOfficeAddress.streetaddress}
                      {this.state.primaryOfficeAddress.locality}
                    </p>
                    <p>
                      {' '}
                      {this.state.primaryOfficeAddress.country},{' '}
                      {this.state.primaryOfficeAddress.zip_code}
                    </p>
                  </figcaption>

                  {this.state.showMoreLocations &&
                  this.state.officeAddress.length > 1 ? (
                    <ul className="dd-list">
                      {this.state.officeAddress.map((item, key) => (
                        <li
                          className="dd-list-item"
                          key={key}
                          onClick={() => this.onLocationSelected(item)}
                        >
                          <figcaption>
                            <p>
                              <a href="#">{item.country}</a>
                            </p>
                            <p>
                              {item.streetaddress}
                              {item.locality}
                            </p>
                            <p>
                              {' '}
                              {item.country}, {item.zip_code}
                            </p>
                          </figcaption>
                        </li>
                      ))}
                    </ul>
                  ) : null}
                </li>
              ) : null}

              {this.state.availableForVirtual ? (
                <li onClick={() => this.selectLocations('V')}>
                  <figure
                    style={
                      this.state.book_appointment_location.type === 'Virtual'
                        ? { borderColor: '#0b9f8d' }
                        : { borderColor: '#fff' }
                    }
                  >
                    <span>
                      <i className="fas fa-video" />
                    </span>
                    <p>Virtual</p>
                  </figure>
                  <figcaption>
                    <p className="text-danger">
                      * The FREE HopJax Mobile App is required for virtual
                      sessions.
                    </p>
                  </figcaption>
                </li>
              ) : null}
              {this.state.availableForInHome ? (
                <li onClick={() => this.selectLocations('I')}>
                  <figure
                    style={
                      this.state.book_appointment_location.type === 'In-Home'
                        ? { borderColor: '#0b9f8d' }
                        : { borderColor: '#fff' }
                    }
                  >
                    <span>
                      <i className="fas fa-home" />
                    </span>
                    <p>In-Home</p>
                  </figure>
                  <figcaption>
                    <p className="text-danger">
                      * In-home appointments must be within 5 miles from my
                      location.
                    </p>
                  </figcaption>
                </li>
              ) : null}
            </ul>
          </div>
        )}
      </div>
    );
  }
}

export default Step1;
