export function Post(type, userData) {
  let BaseURL = ' http://66.175.217.67:8444/api/';

  return new Promise((resolve, reject) => {


    fetch(BaseURL + type, {
      method: 'POST',
      headers:
        //{'Accept': 'application/json',
        { 'Content-Type': 'application/json', 'mode': 'no-cors' },
      //mode: 'no-cors',
      body: JSON.stringify(userData)
    })
      .then((response) => response.json())
      // .then((response) => console.log("ressssss", response , JSON.stringify(response)))
      .then((res) => {
        resolve(res);
      })
      .catch((error) => {
        reject(error);
        console.log("error is", error)
      });
    //   .then((response) => {
    //     console.log("object",response)  
    //     return response.json()})
    //   .then((res) => {
    //     resolve(res);
    //   })
    //   .catch((error) => {
    //     reject(error);
    //   });
    // console.log("object======>>>>>",data)

  });

}
