    rateTheProviderHandler = (e) => {
        const userid = this.props.location.state.userid
        if (this.state.star1 !== 0 && this.state.star2 === 0 && this.state.star3 === 0) {
            this.setState({ totalstar: this.state.star1 })
        }
        if (this.state.star2 !== 0 && this.state.star1 === 0 && this.state.star3 === 0) {
            this.setState({ totalstar: this.state.star2 })
        }
        if (this.state.star3 !== 0 && this.state.star2 === 0 && this.state.star1 === 0) {
            this.setState({ totalstar: this.state.star3 })
        }
        if (this.state.star1 !== 0 && this.state.star2 !== 0 && this.state.star3 === 0) {
            this.setState({ totalstar: (this.state.star1 + this.state.star2) / 2 })
        }
        if (this.state.star2 !== 0 && this.state.star3 !== 0 && this.state.star1 === 0) {
            this.setState({ totalstar: (this.state.star3 + this.state.star2) / 2 })
        }
        if (this.state.star1 !== 0 && this.state.star3 !== 0 && this.state.star2 === 0) {
            this.setState({ totalstar: (this.state.star1 + this.state.star3) / 2 })
        }
        if (this.state.star1 !== 0 && this.state.star2 !== 0 && this.state.star3 !== 0) {
            this.setState({ totalstar: (this.state.star1 + this.state.star2 + this.state.star3) / 3 })
        }
        var rating = {
            userId: userid,
            star: this.state.totalstar
        }
        e.preventDefault(e)
        PostAuth(`appointment/addrating/${userid}`, rating).then((result) => {
            let responseJSON = result;
            console.log("Response is here", responseJSON)
            if (responseJSON) {
                console.log("msg are here", responseJSON)
            } else {
                alert("Some error encountered")
            }

        })
        alert("Rating successfully submitted")
    }


