  componentDidMount = async () => {
    if (localStorage.getItem('token') != '') {
      let user_data = await localStorage.getItem(USER_DATA);
      if (user_data != undefined && JSON.parse(user_data) != undefined) {
        user_data = JSON.parse(user_data);
        console.log(user_data);
        this.setState(
          {
            isloginState: true,
            userFirstName: user_data.firstName,
            userMiddleName: user_data.middleName,
            userLastName: user_data.lastName,
            user_data
          },
          () => {
            this.getDetails();
          }
        );
      }
    } else {
      this.setState({ isloginState: false });
    }
  };

  getDetails = () => {
    var user = {
      month: this.state.monthNum,
      year: this.state.displayYear
    };

    PostAuth('appointment/upcomingappointment', user).then(result => {
      let responseJSON = result;
      console.log('-=-=-=-=-=-=-=-=', responseJSON);
      if (responseJSON) {
        this.setState({
          appointments: responseJSON.data
        });
        console.log('userdetails are here', this.state.appointments);
      } else {
        alert('Enter valid credentials');
      }
    });
  };
  hideModal = () => {
    this.setState({
      show: false
    });
  };

  showModal = () => {
    this.setState({
      show: true
    });
  };

  onArrowButtonClick = month => {
    let currentDate = this.state.currentDate;
    if (month == 1) {
      currentDate.add(1, 'months');
    } else {
      currentDate.subtract(1, 'months');
    }
    this.setState(
      {
        currentDate: currentDate,
        displayMonth: currentDate.format('MMM'),
        monthNum: currentDate.format('M'),
        displayYear: currentDate.format('YYYY')
      },
      () => {
        this.getDetails();
      }
    );
  };
