import React, { Component } from 'react';
import axios from 'axios';
class Resetpassword extends Component {
    constructor() {
        super();
    
        this.state = {
          email: '',
          showError: false,
          messageFromServer: '',
          showNullError: false,
        };
      }
    
      handleChange = name => event => {
        this.setState({
          [name]: event.target.value,
        });
      };
    
      sendEmail = e => {
        e.preventDefault();
        if (this.state.email === '') {
          this.setState({
            showError: false,
            messageFromServer: '',
            showNullError: true,
          });
        } else {
          axios
            .post('http://66.175.217.67:8443/api/user/forgot', {
              email: this.state.email,
            })
            .then(response => {
              console.log(response.data);
              if (response.data.message === 'No account with that email address exists.') {
                this.setState({
                  showError: true,
                  messageFromServer: '',
                  showNullError: false,
                });
              } 
              else if (response.data.message === 'The Password recovery email has been sent to the user email address.') {
                this.setState({
                  showError: false,
                  messageFromServer: 'The Password recovery email has been sent to the user email address.',
                  showNullError: false,
                });
              }
            })
            .catch(error => {
              console.log(error.data);
            });
        }
      };
    render() {
        const { email, messageFromServer, showNullError, showError } = this.state;
        return (
            <div className="reset-password">
                <h1 className="title">Reset your password</h1>
                <form>
                    <input type="email" name="email" onChange={this.handleChange('email')} value={email} />
                    <button className="btn btn-success" onClick={this.sendEmail} >SEND</button>

                    {showNullError && (
          <div>
            <p>The email address cannot be null.</p>
          </div>
        )}
        {showError && (
          <div>
            <p> That email address isn't recognized. Please try again or register for a new account. </p>
         </div>
        )}
        {messageFromServer === 'The Password recovery email has been sent to the user email address.' && (
          <div>
            <h3>Password Reset Email Successfully Sent!</h3>
          </div>
        )}
 
                </form>
                


            </div>

        );
    }
}

export default Resetpassword;