import React, { Component } from 'react';
import { PostData } from '../../Sevices/Postdata';
import FacebookLogin from 'react-facebook-login';
import GoogleLogin from 'react-google-login';
import Appointment from '../../screens/appointment/appointment';
import { withRouter } from 'react-router';
import './login.css';
import { Redirect, Link } from 'react-router-dom';
import config from '../../../src/config.json';
import { asyncLocalStorage } from '../helper';
import moment from 'moment'
import Step1 from '../../screens/appointment/step1';
import LoginPageHeader from '../../component/header/LoginPageHeader';
import { IS_LOGGED_IN, USER_DATA, LOGGED_IN_TIME, TOKEN_DATA } from '../Constant';
class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      formErrors: { firstName: '',password:'', verifyPassword:'', lastName: '', phoneNo: '', email2: '' },
      formValidity: {
        firstName: false,
        lastName: false,
        phoneNo: false,
        email2: false,
        password:false,
        verifyPassword:false
      },
      canSubmit: false,
      email: '',
      passwordLogin:'',
      password: '',
      verifyPassword:'',
      redirectToReferrer: false,
      firstName: '',
      lastName: '',
      phoneNo: '',
      email2: '',
      token: '',
      //isAuthenticated: false, user: null, token: ''
      fbfirstname: '',
      fblastname: '',
      fbemail: '',
      provider: '',
      fbaccessToken: '',
      googleaccessToken: '',
      googlefirstname: '',
      googlelastname: '',
      googlemail: ''
    };

    // console.log("==================================", localStorage.getItem('token'))
  }

  componentDidMount() {
    // this.signOut()
    // let authname = window.gapi
    // console.log(authname)
    // console.log("Rawal")
    // const auth2 = window.gapi.auth2.getAuthInstance()
    // if (auth2 != null) {
    //   auth2.signOut().then(
    //     auth2.disconnect().then(this.props.onLogoutSuccess)
    //   )
    // }
  }

  // signOut() {
  //     const auth2 = window.gapi.auth2.getAuthInstance()
  //     if (auth2 != null) {
  //         auth2.signOut().then(
  //             auth2.disconnect().then(this.props.onLogoutSuccess)
  //         )
  //     }
  // }

  logout = () => {
    asyncLocalStorage.setItem('token', '').then(() => {
      this.setState({ redirectToReferrer: false });
    });
  };

  responseFacebook = response => {
    if (response.name && response.email && response.accessToken) {
      var str = response.name;
      var res = str.split(' ');
      this.setState({
        fbfirstname: res[0],
        fblastname: res[1],
        fbemail: response.email,
        fbaccessToken: response.accessToken,
        provider: 'facebook'
      });
    }
  };
  responseGoogle = response => {
    if (response.profileObj) {
      this.setState(
        {
          googlemail: response.profileObj.email,
          googlefirstname: response.profileObj.givenName,
          googlelastname: response.profileObj.familyName,
          googleaccessToken: response.profileObj.googleId,
          provider: 'google'
        }
        //,() => {this.googleloginHandler()}
      );
      this.googleloginHandler();
    }
  };

  googleloginHandler = () => {
    var googleuserdetail = {
      firstName: this.state.googlefirstname,
      lastName: this.state.googlelastname,
      email: this.state.googlemail,
      provider: this.state.provider,
      accessToken: this.state.googleaccessToken
    };
    if (this.state.googlemail && this.state.googleaccessToken) {
      PostData('user/socialsignin', googleuserdetail).then(result => {
        let responseJSON = result;
        console.log('-=-=-=-=-=-=-=-=', responseJSON);
        if (responseJSON.success === true) {
          asyncLocalStorage
            .setItem('token', responseJSON.data.token)
            .then(() => {
              this.setState({
                redirectToReferrer: true
              });
            });
          asyncLocalStorage.setItem(TOKEN_DATA, responseJSON.data.token)
          asyncLocalStorage.setItem(IS_LOGGED_IN, true)
          asyncLocalStorage.setItem(LOGGED_IN_TIME, moment().format("DD/MM/YYYY") + "@" + moment().format("hh:mm"))
          asyncLocalStorage.setItem(USER_DATA, JSON.stringify(responseJSON.data.socialuser))
        } else {
          alert(responseJSON.message);
        }
      });
    }
  };

  loginHandler = () => {
    var userdetailobj = {
      email: this.state.email,
      password: this.state.passwordLogin
    };
    if (this.state.email && this.state.passwordLogin) {
      PostData('user/signin', userdetailobj).then(result => {
        let responseJSON = result;
        if (responseJSON.data) {
          asyncLocalStorage
            .setItem(
              USER_DATA,
              JSON.stringify(responseJSON.data.userCredentials)
            )
            .then(() => {
              const token = responseJSON.data.token;
              asyncLocalStorage.setItem('token', token).then(() => {
                asyncLocalStorage.setItem(IS_LOGGED_IN, true)
                asyncLocalStorage.setItem(LOGGED_IN_TIME, moment().format("DD/MM/YYYY") + "@" + moment().format("hh:mm"))
                this.setState({
                  redirectToReferrer: true
                });
              });
            })
            .catch(err => { });
        } else {
          alert('Enter valid credentials');
        }
      });
    }
  };

  fbLoginHandler = () => {
    var fbuserdetail = {
      firstName: this.state.fbfirstname,
      lastName: this.state.fblastname,
      email: this.state.fbemail,
      provider: this.state.provider,
      accessToken: this.state.fbaccessToken
    };
    if (this.state.fbemail && this.state.fbaccessToken) {
      PostData('user/socialsignin', fbuserdetail).then(result => {
        // console.log("======>>>", result)

        let responseJSON = result;
        if (responseJSON.data) {
          asyncLocalStorage.setItem('userData', responseJSON);
          asyncLocalStorage.setItem(TOKEN_DATA, responseJSON.data.token)
          asyncLocalStorage.setItem(IS_LOGGED_IN, true)
          asyncLocalStorage.setItem(LOGGED_IN_TIME, moment().format("DD/MM/YYYY") + "@" + moment().format("hh:mm"))
          asyncLocalStorage.setItem(USER_DATA, JSON.stringify(responseJSON.data.socialuser))
          //sessionStorage.setItem('userData', responseJSON);
          this.props.history.push('/search')
          this.setState({
            redirectToReferrer: true
          });
          // this.props.IsLoginHandler();
        } else {
          alert('Enter valid credentials');
        }
      });
    }
  };

  signUpHandler = () => {
    var newuserdetailobj = {
      email: this.state.email2,
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      phoneNo: this.state.phoneNo,
      password:this.state.password
    };
    if (this.state.email2) {
      PostData('user', newuserdetailobj).then(result => {
        let responseJSON = result;
        //alert(responseJSON.message)
        if (responseJSON.data) {
          sessionStorage.setItem('userData', responseJSON);
          alert(responseJSON.message);
          //this.setState({redirectToReferrer: true});
        } else {
          alert(responseJSON.message);
        }
      });
    } else {
      //alert(responseJSON.message)
    }
    this.setState({firstName: '',lastName:'',phoneNo:'',password:'',email2:'',verifyPassword:''})
  };

  onChangeHandler = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  handleChange = event => {
    const { name, value } = event.target;
    this.setState(
      {
        [name]: value
      },
      function () {
        this.validateField(name, value);
      }
    );
  };

  validateField(name, value) {
    const fieldValidationErrors = this.state.formErrors;
    const validity = this.state.formValidity;
    //const isEmail = name === "email"
    const isEmail2 = name === 'email2';
    //const isPassword = name === "password"
    const isPhoneNo = name === 'phoneNo';
    const isfirstName = name === 'firstName';
    const islastName = name === 'lastName';
    const ispasswordName = name === 'password';
    const isVerifyPasswordName = name === 'verifyPassword';
    const emailTest = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;

    validity[name] = value.length > 0;
    fieldValidationErrors[name] = validity[name]
      ? ''
      : `${name} is required and cannot be empty`;

    if (validity[name]) {
      if (isfirstName) {
        validity[name] = value.length >= 3;
        fieldValidationErrors[name] = validity[name]
          ? ''
          : `${name} should be a valid entry`;
      }
      if (islastName) {
        validity[name] = value.length >= 3;
        fieldValidationErrors[name] = validity[name]
          ? ''
          : `This should be a valid entry`;
      }
      //   if(isEmail){
      //     validity[name] = emailTest.test(value);
      //     fieldValidationErrors[name] = validity[name] ? '' : `This should be a valid email address`;
      //   }
      if (isEmail2) {
        validity[name] = emailTest.test(value);
        fieldValidationErrors[name] = validity[name]
          ? ''
          : `This should be a valid email address`;
      }
      if (isPhoneNo) {
        validity[name] = value.length >= 9;
        fieldValidationErrors[name] = validity[name]
          ? ''
          : `This should be a valid entry`;
      }
      //   if(isPassword){
      //     validity[name] = value.length >= 5;
      //     fieldValidationErrors[name] = validity[name] ? '': `Password should be 5 characters or more`;
      //   }
    }

    this.setState(
      {
        formErrors: fieldValidationErrors,
        formValidity: validity
      },
      () => this.canSubmit()
    );
  }
  errorClass(error) {
    return error.length === 0 ? '' : 'is-invalid';
  }

  canSubmit = () => {
    this.setState({
      canSubmit:
        this.state.formValidity.email2 &&
        this.state.formValidity.firstName &&
        this.state.formValidity.lastName &&
        this.state.formValidity.phoneNo
    });
  };

  render() {
    if (this.state.redirectToReferrer === true) {
      // return (<Redirect to={"/provider"} />)
      if (this.props.loginFromAppointment === true) {
        return <Step1 userid={this.props.location.state.userid} />;
      }
      if (this.props.loginFromMapView === true) {
        return <Step1 userid={this.props.location.state.userid} />;
      } else {
        return <Redirect to={'/provider'} />;
      }
    }

    //   if(asyncLocalStorage.getItem('userData')){
    //     return (<Redirect to={"/admin/provider"}/>)
    //   }
    return (
      <div className="contain-wrapper">
        <LoginPageHeader />
        <div
          className="login-panel"
          style={{ backgroundImage: 'url(/images/banner_bg.png)' }}
        >
          {/* {content} */}

          <div className="container">
            <div className="row">
              <div className="col-md-6">
                <div className="login">
                  <article className="ribban">
                    <img src={'/images/login_ribban.png'} />
                  </article>
                  <section className="logcontent text-left">
                    <small>EXISTING USERS</small>
                    <h1 className="title">Secure Login</h1>
                    <div className="form-group">
                      <input
                        type="text"
                        name="email"
                        placeholder="Username"
                        //className={`form-control ${this.errorClass(this.state.formErrors.email)}`}
                        value={this.state.email}
                        className="form-control"
                        onChange={this.onChangeHandler}
                      />
                      {/* <div className="invalid-feedback">{this.state.formErrors.email}</div> */}
                    </div>
                    <div className="form-group">
                      <input
                        type="password"
                        name="passwordLogin"
                        placeholder="Password"
                        value={this.state.passwordLogin}
                        className="form-control"
                        onChange={this.onChangeHandler}
                      />
                    </div>
                    <div className="form-group">
                      <button
                        className="btn btn-success"
                        onClick={this.loginHandler}
                      >
                        Log in
                      </button>
                    </div>
                    {/* <a href="#" className="text-primary"  >Forgot Your Username or Password?</a> */}
                    <Link to={'/reset'} className="text-primary">
                      Forgot Your Username or Password?
                    </Link>
                  </section>
                </div>
              </div>
              <div className="col-md-6">
                <div className="signup text-left">
                  <small>NEW USERS</small>
                  <h1>Create A HopJax&trade; Account</h1>
                  <p>
                    Creating a HopJax&trade; account is fast, easy and FREE!
                  </p>
                  <div className="row">
                    <div className="col-sm-6">
                      <FacebookLogin
                        appId="344149956372808"
                        textButton="Sign in with Facebook"
                        autoLoad={false}
                        fields="name,email,picture"
                        onClick={this.fbLoginHandler}
                        callback={this.responseFacebook}
                        cssClass="my-facebook-button-class"
                        icon="fa-facebook"
                      />
                      {/* <a href="#" className="btn btn-primary"><i className="fab fa-facebook-f"></i>&nbsp;Sign in with Facebook</a> */}
                    </div>
                    <div className="col-sm-6">
                      <div className="google_login">
                        <GoogleLogin
                          clientId="475611865645-dp5vdm7cqon5t9ik87g262trtbvaomeh.apps.googleusercontent.com"
                          buttonText="Sign in with Google"
                          onSuccess={this.responseGoogle}
                          onFailure={this.responseGoogle}
                          className="google_login"
                        //onClick ={this.googleloginHandler}
                        // render={renderProps => (
                        //     <button onClick={this.googleloginHandler}>This is my custom Google button</button>
                        //   )}
                        />
                      </div>
                      {/* <a href="/reset" className="btn btn-danger"><i className="fab fa-google-plus-g"></i>&nbsp;Sign in with Google</a> */}
                    </div>
                    <div className="col-sm-12">
                      <figure className="or">
                        <img src={'/images/login_or.png'} />
                      </figure>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <input
                          type="text"
                          name="firstName"
                          placeholder="First Name"
                          value={this.state.firstName}
                          className={`form-control ${this.errorClass(
                            this.state.formErrors.firstName
                          )}`}
                          //className="form-control"
                          onChange={this.handleChange}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.firstName}
                        </div>
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <input
                          type="text"
                          name="lastName"
                          placeholder="Last Name"
                          value={this.state.lastName}
                          className={`form-control ${this.errorClass(
                            this.state.formErrors.lastName
                          )}`}
                          //className="form-control"
                          onChange={this.handleChange}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.lastName}
                        </div>
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <input
                          type="text"
                          name="phoneNo"
                          placeholder="Phone Number"
                          value={this.state.phoneNo}
                          className={`form-control ${this.errorClass(
                            this.state.formErrors.phoneNo
                          )}`}
                          //className="form-control"
                          onChange={this.handleChange}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.phoneNo}
                        </div>
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <input
                          type="email"
                          name="email2"
                          placeholder="Email Address"
                          value={this.state.email2}
                          //className="form-control"
                          className={`form-control ${this.errorClass(
                            this.state.formErrors.email2
                          )}`}
                          onChange={this.handleChange}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.email2}
                        </div>
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <input
                          type="password"
                          name="password"
                          placeholder="Password"
                          value={this.state.password}
                          //className="form-control"
                          className={`form-control ${this.errorClass(
                            this.state.formErrors.password
                          )}`}
                          onChange={this.handleChange}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.password}
                        </div>
                      </div>
                    </div>
                    <div className="col-sm-6">
                      <div className="form-group">
                        <input
                          type="password"
                          name="verifyPassword"
                          placeholder="Verify Password"
                          value={this.state.verifyPassword}
                          //className="form-control"
                          className={`form-control ${this.errorClass(
                            this.state.formErrors.verifyPassword
                          )}`}
                          onChange={this.handleChange}
                        />
                        <div className="invalid-feedback">
                          {this.state.formErrors.verifyPassword}
                        </div>
                      </div>
                    </div>
                    <div className="col-sm-12">
                      <div className="form-group">
                        <button
                          className="btn btn-filter"
                          onClick={this.signUpHandler}
                        >
                          Create My Account
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(Login);
