import {
  getRequest,
  postRequest,
  patchRequest,
  deleteRequest
} from '../../Utility/helper';
import { SERVER_URL, PROVIDER_SERVER_URL } from '../../Utility/Constant';

export const getScheduleApi = (id, data) =>
  postRequest(SERVER_URL, `appointment/getschedule/${id}`, data);

export const getAttendeesApi = () =>
  getRequest(SERVER_URL, `user/getattendees`);

export const attendeeApi = data =>
  postRequest(SERVER_URL, `user/attendee`, data);

export const scheduleMyAppointmentApi = data =>
  postRequest(SERVER_URL, `appointment/createappointment`, data);

export const getSingleProvider = id =>
  getRequest(PROVIDER_SERVER_URL, `provider/${id}`);

export const reportProviderApi = id =>
  getRequest(PROVIDER_SERVER_URL, `provider/${id}`);

export const addRatingApi = (id, data) =>
  postRequest(SERVER_URL, `appointment/addrating/${id}`, data);

export const sendMessageApi = (id, data) =>
  postRequest(PROVIDER_SERVER_URL, `provider/sentmsgprovider/${id}`, data);
