import React, { Component } from 'react';
import axios from 'axios';
import DatePicker from 'react-date-picker';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import Loader from 'react-loader-spinner';
import AddUser from './AddUser';
import { asyncLocalStorage } from '../../common/helper';
import { getAttendeesApi, attendeeApi } from './api';
class Step3 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      firstName: '',
      lastName: '',
      relationship: '',
      dob: '',
      getattendeedata: {},
      attributeForm: [],
      data1: '',
      data2: '',
      arraydata: [],
      listarraystate: [],
      addedPersonalHolderData: [],
      personsHolder: [],
      isClicked: false,
      count: 2,
      finalAppointmentPayload: {}
    };
    //this.addAttributeForm();
  }

  onChange = date => this.setState({ date });

  onChangeHandler = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  removeElementHandler = e => {
    e.preventDefault();
    //debugger
    var array = this.state.attributeForm;
    var position = array.length - 2;
    array.splice(position, 1);
    this.setState({ attributeForm: array });
    console.log('wjwbhjwbukjb', array);
  };

  componentDidMount() {
    window.scrollTo(0, 0);
    if (this.props.finalAppointmentPayload != undefined) {
      let appointment = this.props.finalAppointmentPayload;
      this.setState(
        {
          finalAppointmentPayload: this.props.finalAppointmentPayload,
          appointment_date:
            appointment.appointment_date != undefined
              ? appointment.appointment_date
              : null,
          scheduled_start_time:
            appointment.scheduled_start_time != undefined
              ? appointment.scheduled_start_time
              : null,
          scheduled_end_time:
            appointment.scheduled_end_time != undefined
              ? appointment.scheduled_end_time
              : null
        },
        () => {
          this.props.showStepZillaNavigation(false);
        }
      );
    }

    //const token = this.props.token
    console.log('token in step3 is', localStorage.getItem('token'));
    const token = localStorage.getItem('token');
    axios
      .get(` http://66.175.217.67:8443/api/user/getattendees`, {
        headers: {
          'Content-Type': 'application/json',
          authenticate: token
        }
      })
      .then(attendeesData => {
        if (
          attendeesData.data.data != undefined &&
          Array.isArray(attendeesData.data.data.relation)
        ) {
          let selfData = {
            _id: attendeesData.data.data._id,
            memberid: attendeesData.data.data._id,
            firstName: attendeesData.data.data.firstName,
            lastName: attendeesData.data.data.lastName,
            dob:
              attendeesData.data.data.dob != undefined
                ? attendeesData.data.data.dob
                : '',
            relationship: 'self',
            isSelected: true
          };
          let relationArray = attendeesData.data.data.relation;

          relationArray = relationArray.map(item => {
            if (item.isSelected == undefined) item.isSelected = false;
            if (item.memberid == undefined) item.memberid = item._id;
            return item;
          });

          relationArray.unshift(selfData);

          this.setState(
            {
              addedPersonalHolderData: relationArray,
              loading: false,
              getattendeedata: attendeesData.data.data
            },
            () => this.props.showStepZillaNavigation(true)
          );
        }
      })
      .catch(error => {
        //on error
        alert('There is an error in API call.');
        console.log('an alert');
      });
  }

  selectMemberForAppointment = data => {
    let relationArray = this.state.addedPersonalHolderData;
    relationArray = relationArray.map(item => {
      if (data.memberid == item.memberid)
        item.isSelected = item.isSelected ? false : true;
      return item;
    });
    this.setState(
      {
        relationArray
      },
      () => {
        this.props.getDataFromStep3();
      }
    );
  };

  onSaveBtnClick = (index, obj) => {
    this.setState({
      loading: true,
      isClicked: false
    });

    attendeeApi(obj)
      .then(addedAttendeeData => {
        if (addedAttendeeData.data.success) {
          if (
            addedAttendeeData.data.data != undefined &&
            Array.isArray(addedAttendeeData.data.data.relation)
          ) {
            let objectRelation = addedAttendeeData.data.data.relation[0];
            // if (objectRelation != undefined) {
            objectRelation.memberid = objectRelation._id;
            objectRelation.isSelected = false;
            let relationArray = this.state.addedPersonalHolderData;
            relationArray.push(objectRelation);
            this.state.personsHolder.splice(index, 1);
            this.setState(
              {
                loading: false,
                addedPersonalHolderData: relationArray,
                personsHolder: this.state.personsHolder
              },
              () => {}
            );
            // } else {
            //     this.showToast('Not added data')
            // }
          }
        } else {
          this.showToast(addedAttendeeData.data.message);
        }
      })
      .catch(err => {
        console.log(err);
        this.showToast(JSON.stringify(err));
      });
  };

  showToast = message => {
    alert(message);
    // this.refs.toast.show(message, DURATION.LENGTH_LONG, () => {
    //     this.setState({ loading: false });
    // })
  };

  onAddBtnClick = () => {
    if (!this.state.isClicked) {
      this.setState(
        {
          personsHolder: this.state.personsHolder.concat({
            title: this.state.count
          }),
          count: this.state.count + 1,
          isClicked: true
        },
        () => console.log('aray after ', this.state.personsHolder)
      );
    }
  };

  onRemoveBtnClick = index => {
    this.state.personsHolder.splice(index, 1);
    this.setState(
      {
        personsHolder: this.state.personsHolder,
        count: this.state.count - 1,
        isClicked: false
      },
      () => console.log('aray after ', this.state.personsHolder)
    );
  };

  getDataFromStep3() {
    let finalAppointmentPayload = this.state.finalAppointmentPayload;
    if (finalAppointmentPayload != undefined) {
      let attendeesTemp = this.state.addedPersonalHolderData.filter(item => {
        return item.isSelected;
      });
      if (attendeesTemp.length > 0) {
        finalAppointmentPayload.attendees = attendeesTemp;
      } else {
        this.showToast('Select on attendee for appointment');
      }
      this.props.getDataFromStep3(finalAppointmentPayload);
    } else {
      this.showToast('Unexpected behaviour');
    }
  }

  render() {
    //console.log("sate is",this.state.data1)
    //this.props.click()
    console.log('value after render', this.state.listarraystate);

    console.log('wfefw', this.state.firstName);
    return (
      <div className="step3 step4">
        {(this.state.getattendeedata.firstName && this.state.listarraystate) !=
        null ? (
          <div>
            <h2 className="text-center text-primary">
              <b>Welcome, {this.state.getattendeedata.firstName}!</b> Who will
              attend this appointment?
            </h2>
            <div className="cencellation">
              <div className="row">
                <div className="col-md-6">
                  <div className="card">
                    {this.state.addedPersonalHolderData != null
                      ? this.state.addedPersonalHolderData.map((node, i) => {
                          return (
                            <label className="checkbox">
                              <input
                                type="checkbox"
                                checked={node.isSelected}
                                onClick={() =>
                                  this.selectMemberForAppointment(node)
                                }
                              />
                              <span>
                                <b>
                                  {node.firstName} {node.lastName}{' '}
                                  {'(' + node.relationship + ')'}
                                </b>
                              </span>
                            </label>
                          );
                        })
                      : null}
                    <div>
                      {this.state.personsHolder.length === 0
                        ? null
                        : this.state.personsHolder.map(item => (
                            <AddUser
                              item={item}
                              removeItem={this.onRemoveBtnClick}
                              onSaveBtnClick={this.onSaveBtnClick}
                            />
                          ))}
                      <div className="form-group add-another">
                        <button
                          className="btn btn-primary"
                          onClick={this.onAddBtnClick}
                        >
                          + Add Another Person
                        </button>
                      </div>
                    </div>

                    <div style={{ height: '101px' }} />
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="details">
                    <h4 className="text-primary">Your Appointment Details</h4>
                    <div className="media">
                      <span className="media-left  media-middle">
                        <i className="far fa-building text-primary" />
                      </span>
                      <p className="media-body">
                        {/* <label> Lynda Griffin Bull, MC, LPC</label>1820 E. Ray Rd. Suite A107<br />Chandler, AZ 85225<br /> */}
                        <label>
                          {' '}
                          {this.props.firstName} {this.props.middleName}{' '}
                          {this.props.lastName}
                        </label>
                        {this.props.streetaddress},{this.props.locality}
                        <br />
                        {this.props.country}
                        <br />
                        {/* <figure>
                                                    <img src={'/images/map.png'} />
                                                </figure> */}
                      </p>
                    </div>
                    <div className="media">
                      <span className="media-left media-middle">
                        <i className="far fa-calendar text-primary" />
                      </span>
                      <p className="media-body">
                        {this.state.appointment_date}
                      </p>
                    </div>
                    <div className="media">
                      <span className="media-left media-middle">
                        <i className="far fa-clock text-primary" />
                      </span>
                      <p className="media-body">
                        {this.state.scheduled_start_time +
                          ' - ' +
                          this.state.scheduled_end_time}
                      </p>
                    </div>
                    {/* <div className="media">
                                            <span className="media-left media-middle"><i className="far fa-user text-primary"></i></span>
                                            <p className="media-body">
                                                <label>Jeremiah Woods</label>
                                                DOB: 3/6/74
                                        </p>
                                        </div> */}
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <div>
            <Loader type="Puff" color="#00BFFF" height="100" width="100" />
          </div>
        )}
      </div>
    );
  }
}

export default Step3;
