import React, { Component } from 'react'
import Modal from 'react-modal';
import { withRouter } from 'react-router';
import { sendMessageApi } from '../appointment/api';
import StarRatingComponent from './StarRatingComponent'
import ReactStars from 'react-stars';
import { USER_DATA, IS_LOGGED_IN } from '../../common/Constant'
import { asyncLocalStorage } from '../../common/helper'
import Alert from '../../common/Alert'

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)'
    }
};


class ExpertProfile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            toggleExpert: 'expert',
            modalIsOpen: false,
            popupType: 'sendMsg',
            profiledata: {},
            nextData: {},
            finalAppointmentPayload: {},
            user_data: {},
            steps: [],
            showNavigation: false,
            providerProfilePic: '',
            providerFirstName: '',
            providerMiddleName: '',
            providerLastName: '',
            providerProfessionalTitle: '',
            providerState: '',
            providerCountry: '',
            messageName: '',
            messageEmail: '',
            messagePhone: '',
            messageSubject: '',
            messageBody: '',
            providerData: {},
            showStarModal: false,
            isLogin: false,
            display: 'none',
            error: false,
            message: ""
        };
    }

    componentDidMount() {
        asyncLocalStorage.getItem(IS_LOGGED_IN)
            .then(data => {
                if (data == true || data == "true") {
                    this.setState({
                        isLogin: true
                    });
                }
            })
        if (this.props.providerData) {
            this.updateFields(this.props.providerData)
        }

        // console.log("this.props", this.props)
    }
    componentWillReceiveProps(nextProps) {

        if (nextProps.providerData != undefined) {
            this.updateFields(nextProps.providerData)
        }
    }

    updateFields = (providerData) => {
        let address = {}
        let checkAddressAvailable = Array.isArray(providerData.address) && providerData.address.length > 0;
        if (checkAddressAvailable) {
            address = providerData.address[0]
        }
        this.setState({
            providerData: providerData,
            providerProfilePic: '',
            providerFirstName: providerData.firstName != undefined ? providerData.firstName : '',
            providerMiddleName: providerData.middleName != undefined ? providerData.middleName : '',
            providerLastName: providerData.lastName != undefined ? providerData.lastName : '',
            providerProfessionalTitle: '',
            providerState: address != undefined && address.region != undefined ? address.region : '',
            providerCountry: address != undefined && address.country != undefined ? address.country : '',
        });
    }

    openModal = (val) => {
        this.setState({ modalIsOpen: true });
        this.setState({ popupType: val });
    }

    closeModal = () => {
        this.setState({ modalIsOpen: false });
    }

    expertTgl = () => {
        if (this.state.toggleExpert === 'expert') {
            this.state.toggleExpert = 'expert open';
        } else {
            this.state.toggleExpert = 'expert';
        }
    };

    onSendBtnClick = (data) => {

        if (this.validateFields()) {
            let data = {
                userName: this.state.messageName,
                userEmail: this.state.messageEmail,
                phoneNo: this.state.messagePhone,
                subject: this.state.messageSubject,
                message: this.state.messageBody
            }

            sendMessageApi(this.state.providerData._id, data)
                .then(messageResponse => {
                    this.showToast(messageResponse.data.message)
                    this.setState({ modalIsOpen: false });
                }).catch(error => {
                    // this.showToast(JSON.stringify(error))
                    if (!error.response) {
                        // network error
                        this.showToast('Network Error')
                    } else {
                        // http status code
                        const code = error.response.status
                        // response data
                        const response = error.response.data
                        this.showToast(error.response.data)
                    }
                })
        }
    }
    showToast = (message) => {
        alert(message)
    }
    validateFields = () => {
        if (this.state.messageName.length === 0) {
            this.showToast('Your Name cannot be empty')
            return false
        }
        if (this.state.messageEmail.length === 0) {
            this.showToast('Email Address cannot be empty')
            return false
        }

        if (this.state.messagePhone.length === 0) {
            this.showToast('Phone Number cannot be empty')
            return false
        }

        if (this.state.messageSubject.length === 0) {
            this.showToast('Message Subject cannot be empty')
            return false
        }

        if (this.state.messageBody.length === 0) {
            this.showToast('Message Body cannot be empty')
            return false
        }

        return true
    }
    handleChange = (ev) => {
        // followed by any state changes, or props callbacks
        this.setState({ [ev.target.name]: ev.target.value })
    }

    showStarModal = () => {
        this.setState({ showStarModal: true });
    };

    showToast = () => {
        this.setState({
          display: 'block',
          error: true,
          message: "You need to login"
        });
        setTimeout(() => {
          this.setState({
            display: 'none',
            error: true,
            message: "You need to login"
          });
        }, 1000)
      }

    render() {
        return (
            <div>
                <Alert
                    display={this.state.display}
                    error={this.state.error}
                    message={this.state.message}
                    />
                <StarRatingComponent
                    show={this.state.showStarModal} />
                <div className={this.state.toggleExpert}>
                    <a href="#" className="therapist-toggle" onClick={this.expertTgl}>
                        <i className="fas fa-user-md" />
                    </a>
                    <figure
                        className="expert-photo"
                        style={{ backgroundImage: 'url(/images/client1.png)' }}
                    />
                    <figcaption>
                        <small className="active">Verifed by HopJax &nbsp;<img src={'/images/green_check_icon.png'} /></small>


                        <p className="rating"
                            onClick={() => this.state.isLogin === true ? this.showStarModal() : alert("You need to login to rate provider")}
                            //onClick={()=>console.log("i am working")}
                            style={{ padding: 10 }}>
                            <ReactStars
                                count={5}
                                value={this.state.providerData.totalstar}
                                size={21}
                                edit={false}
                                color2={'#ffd700'} />
                        </p>
                        <button className="btn btn-primary" onClick={async () => {
                            let isLoggedIn = await localStorage.getItem(IS_LOGGED_IN)

                            if (isLoggedIn == "true" || isLoggedIn == true) {
                                this.props.history.push({
                                pathname: `/appointment/${this.state.providerData._id}`,
                                state: { provider_id: this.state.providerData._id }
                                })
                            }else{
                                this.showToast()
                            }
                        }}>Book An Appointment</button>
                        <button
                            className="btn btn-filter"
                            onClick={() => this.openModal('sendMsg')}
                        >
                            Send Me A Message
                </button>
                        <button className="btn btn-filter">Website</button>
                        <button
                            className="btn btn-filter"
                            onClick={() => this.openModal('sendFrnd')}
                        >
                            Send To A Friend
                </button>
                        <button className="btn btn-filter">Report</button>
                    </figcaption>
                </div>
                <Modal
                    isOpen={this.state.modalIsOpen}
                    onAfterOpen={this.afterOpenModal}
                    onRequestClose={this.closeModal}
                    style={customStyles}
                    contentLabel="Example Modal">
                    {this.state.popupType == 'sendFrnd' ? (
                        <div className="modal-popup">
                            <button className="close" onClick={this.closeModal}>
                                Close Window &nbsp; &nbsp;
                <i className="far fa-window-close" />
                            </button>
                            <div className="intro-send-message">
                                <div className="media">
                                    <div className="media-left">
                                        <figure
                                            style={{ backgroundImage: 'url(/images/client_1.png)' }}
                                        />
                                        <i className="crt">
                                            <img src={'/images/green_check_icon.png'} />
                                        </i>
                                    </div>
                                    <div className="media-body">
                                        <small className="text-primary">Send A Message To:</small>
                                        <h6>{this.state.providerFirstName} {this.state.providerMiddleName} {this.state.providerLastName}</h6>
                                        <p>
                                            Counselor/Therapist |{' '}
                                            <b className="active">{this.state.providerState} {this.state.providerCountry}</b>
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div className="form-container">
                                <h4 className="text-primary">Your Information</h4>
                                <form className="row">
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <input
                                                type="text"
                                                placeholder="Your Name"
                                                className="form-control"
                                            />
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <input
                                                type="Email"
                                                placeholder="Your Email Address"
                                                className="form-control"
                                            />
                                        </div>
                                    </div>
                                    <div className="col-sm-12">
                                        <div className="form-group">
                                            <hr />
                                            <h4 className="text-primary">
                                                Your Friend's Information
                      </h4>
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <input
                                                type="Number"
                                                placeholder="Your Friend's Name"
                                                className="form-control"
                                            />
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <input
                                                type="Email"
                                                placeholder="Your Friend's Email Address"
                                                className="form-control"
                                            />
                                        </div>
                                    </div>
                                    <div className="col-sm-12">
                                        <div className="form-group">
                                            <textarea className="form-control" />
                                        </div>
                                    </div>
                                    <div className="col-sm-12">
                                        <div className="form-group">
                                            <img src={'/images/captcha.png'} />
                                        </div>
                                    </div>
                                    <div className="col-sm-6">
                                        <div className="form-group">
                                            <button className="btn btn-primary">
                                                <i className="far fa-paper-plane" />
                                                &nbsp; &nbsp; Send Message
                      </button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    ) : (
                            <div className="modal-popup">
                                <button className="close" onClick={this.closeModal}>
                                    Close Window &nbsp; &nbsp;
                <i className="far fa-window-close" />
                                </button>
                                <div className="intro-send-message">
                                    <div className="media">
                                        <div className="media-left">
                                            <figure
                                                style={{ backgroundImage: 'url(/images/client_1.png)' }}
                                            />
                                            <i className="crt">
                                                <img src={'/images/green_check_icon.png'} />
                                            </i>
                                        </div>
                                        <div className="media-body">
                                            <small className="text-primary">Send A Message To:</small>
                                            <h6>{this.state.providerFirstName} {this.state.providerMiddleName} {this.state.providerLastName}</h6>
                                            <p>
                                                Counselor/Therapist |{' '}
                                                <b className="active">{this.state.providerState}, {this.state.providerCountry}</b>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <div className="form-container">
                                    <div className="row">
                                        <div className="col-sm-6">
                                            <div className="form-group">
                                                <input
                                                    type="text"
                                                    placeholder="Your Name"
                                                    className="form-control"
                                                    name="messageName"
                                                    onChange={this.handleChange}
                                                />
                                            </div>
                                            <div className="form-group">
                                                <input
                                                    type="Email"
                                                    placeholder="Your Email Address"
                                                    className="form-control"
                                                    onChange={this.handleChange}
                                                    name="messageEmail"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <input
                                                    type="Number"
                                                    placeholder="Your Phone Number"
                                                    className="form-control"
                                                    onChange={this.handleChange}
                                                    name="messagePhone"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <input
                                                    type="text"
                                                    placeholder="Message Subject"
                                                    className="form-control"
                                                    onChange={this.handleChange}
                                                    name="messageSubject"
                                                />
                                            </div>
                                            <div className="form-group">
                                                <textarea className="form-control"
                                                    onChange={this.handleChange}
                                                    name="messageBody" />
                                            </div>
                                            <div className="form-group">
                                                <img src={'/images/captcha.png'} />
                                            </div>
                                            <div className="form-group">
                                                <button className="btn btn-primary"
                                                    onClick={this.onSendBtnClick}>
                                                    <i className="far fa-paper-plane" />
                                                    &nbsp; &nbsp; Send Message
                      </button>
                                            </div>
                                        </div>
                                        <div className="col-sm-6">
                                            <p className="text-primary">
                                                HopJax Therapists are here to help! Ask us about
                                                anything. This is your initial contact so be short and
                      to-the-point.{' '}
                                            </p>
                                            <hr />
                                            <h4 className="active">When will I get a response?</h4>
                                            <p>
                                                Typical response time is 24-48 hours. If yoiu don't
                                                revceive a response, please email us at:
                    </p>
                                            <hr />
                                            <h4 className="active">Can I book an appointment?</h4>
                                            <p>
                                                Yes! <span>Click here to book an appointment.</span>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <p>
                                    HopJax does not read or retain your email. However, a copy will
                                    be sent to you for your records. Please be aware that email is
                                    not a secure means of communication and spam filters may prevent
                                    your email from reaching the therapist. The therapist should
                                    respond to you by email, although we recommend that you follow
                                    up with a phone call. If you prefer corresponding via phone,
                                    leave your contact number.
              </p>
                                <p>
                                    Sending an email using this page does not guarantee that the
                                    recipient will receive, read or respond to your email.
                <b>
                                        If this is an emergency do not use this form, call 911 or your
                                        nearest hospital.
                </b>
                                </p>
                            </div>
                        )}
                </Modal>
            </div>
        )
    }
}

export default withRouter(ExpertProfile)