import React, { Component } from 'react';
import ReactDOM from "react-dom";
import { Map, InfoWindow, Marker, GoogleApiWrapper } from 'google-maps-react';

export default class InfoWindowEx extends Component {
    constructor(props) {
        super(props);
        this.infoWindowRef = React.createRef();
        this.onInfoWindowOpen = this.onInfoWindowOpen.bind(this);
        if (!this.containerElement) {
            this.containerElement = document.createElement(`div`);
        }
        this.state = {
            providerList: []
        }
    }

    onInfoWindowOpen = () => {
        ReactDOM.render(
            // function universalChildrenOnly(children) {
            //     return (Array.isArray (children) && children.length === 1)
            //       ? children[0]
            //       : React.Children.only(children),this.containerElement;
            //   }
            React.Children.only(this.props.children),
            this.containerElement
        );
        this.infoWindowRef.current.infowindow.setContent(this.containerElement);
    }
    render() {
        return (
            <InfoWindow
                onOpen={this.onInfoWindowOpen}
                ref={this.infoWindowRef}
                {...this.props}
            />
        );
    }
}