import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { withRouter } from 'react-router';
import Modal from 'react-modal';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import Steps, { Step } from 'rc-steps';
import StepZilla from 'react-stepzilla';
import Step1 from './step1';
import Step2 from './step2';
import Step3 from './step3';
import Step4 from './step4';
import '../therapist/therapist.css';
import './appointment.css';
import axios from 'axios';
import { asyncLocalStorage } from '../../common/helper';
import { USER_DATA } from '../../Utility/Constant';
import { getSingleProvider, sendMessageApi } from './api';
import Header from '../../component/header';
import ExpertProfile from '../therapist/ExpertProfile';
import { IS_LOGGED_IN } from '../../common/Constant';

// Make sure to bind modal to your appElement (http://reactcommunity.org/react-modal/accessibility/)
Modal.setAppElement('#root');

const state = {
    finalAppointmentPayload: {},
    user_data: {}
};

class Appointment extends Component {
    constructor(props) {
        super(props);
        this.state = {
            toggleExpert: 'expert',
            modalIsOpen: false,
            popupType: 'sendMsg',
            profiledata: {},
            nextData: {},
            finalAppointmentPayload: {},
            user_data: {},
            steps: [],
            showNavigation: false,
            loggedIn : false
        };
    }
    componentWillMount(){
        let loggedIn = this.getlogedData();
        this.setState({loggedIn : loggedIn});
    }

    async getlogedData(){
        return await localStorage.getItem(IS_LOGGED_IN);
    }
    componentDidMount() {
        console.log("=====>>>", this.props)
        if (this.props != undefined && this.props.location != undefined
            && this.props.location.state != undefined && this.props.location.state.userid != undefined) {
            const provider_id = this.props.location.state.userid


            // const provider_id = navigation.getParam('provider_id', '');
            if (provider_id != undefined) {
                this.getDataFromAPI(provider_id)
            }
        } else {
            const provider_id = this.props.match.params.id

            if (provider_id != undefined) {
                this.getDataFromAPI(provider_id)
            }
        }
    }

    getDataFromAPI = provider_id => {
        this.setState({
            loading: true
        });
        getSingleProvider(provider_id)
            // .then(data => data.json())
            .then(providerData => {
                if (providerData.data.success) {
                    if (providerData.data.data[0]) {
                        this.updateFields(providerData.data.data[0]);
                    }
                } else {
                    this.showToast(providerData.data.message);
                }
            })
            .catch(err => {
                this.showToast(JSON.stringify(err));
            });
    };

    updateFields = providerData => {
        let checkAddressAvailable =
            Array.isArray(providerData.address) && providerData.address.length > 0;

        this.setState(
            {
                loading: false,
                provider_id: providerData._id,
                providerData: providerData,
                providerFirstName:
                    providerData.firstName != undefined ? providerData.firstName : '',
                providerMiddleName:
                    providerData.middleName != undefined ? providerData.middleName : '',
                providerLastName:
                    providerData.lastName != undefined ? providerData.lastName : '',
                providerProfile: '',
                providerRatingNumber: providerData.totalrating,
                providerRatingStar: providerData.totalstar,
                providerEmail: '',
                providerPhoneNumber: '',
                providerAddressRegion: checkAddressAvailable
                    ? providerData.address[0].region
                    : '',
                providerAddressformatted: checkAddressAvailable
                    ? providerData.address[0].formatted
                    : '',
                providerAddressCountry: checkAddressAvailable
                    ? providerData.address[0].country
                    : ''
            },
            () => {
                this.setTheSteps();
            }
        );
    };





    getDataFromStep1 = appointment_location => {
        let finalAppointmentPayload = state.finalAppointmentPayload;
        if (!this.isEmpty(appointment_location)) {
            finalAppointmentPayload.appointment_location = appointment_location;
            state.finalAppointmentPayload = {
                ...state.finalAppointmentPayload,
                finalAppointmentPayload
            };
            this.setState({
                finalAppointmentPayload,
                showNavigation: true
            });
        }
    };
    getDataFromStep2 = finalAppointmentPayload => {
        if (!this.isEmpty(finalAppointmentPayload)) {
            state.finalAppointmentPayload = {
                ...state.finalAppointmentPayload,
                finalAppointmentPayload
            };
            this.setState({
                finalAppointmentPayload,
                showNavigation: true
            });
        }
    };

    getDataFromStep3 = finalAppointmentPayload => {
        if (!this.isEmpty(finalAppointmentPayload)) {
            state.finalAppointmentPayload = {
                ...state.finalAppointmentPayload,
                finalAppointmentPayload
            };
        }
        this.setState({
            finalAppointmentPayload,
            showNavigation: true
        });
    };

    isEmpty = obj => {
        for (var key in obj) {
            if (obj.hasOwnProperty(key)) return false;
        }
        return true;
    };

    showStepZillaNavigation = show => {
        this.setState({
            showNavigation: show
        });
    };
    setTheSteps = () => {
        let steps = [
            {
                name: 'BOOK AN APPOINTMENT',
                component: (
                    <Step1
                        {...this.props}
                        provider_id={this.state.provider_id}
                        finalAppointmentPayload={state.finalAppointmentPayload}
                        getDataFromStep1={this.getDataFromStep1}
                        showStepZillaNavigation={this.showStepZillaNavigation}
                    />
                )
            },
            {
                name: 'Date and Time',
                component: (
                    <Step2
                        provider_id={this.state.provider_id}
                        finalAppointmentPayload={state.finalAppointmentPayload}
                        getDataFromStep2={this.getDataFromStep2}
                        showStepZillaNavigation={this.showStepZillaNavigation}
                    />
                )
            },
            {
                name: 'Attendees',
                component: (
                    <Step3
                        provider_id={this.state.provider_id}
                        //userid={this.userid}
                        firstName={this.state.providerData.firstName}
                        middleName={this.state.providerData.middleName}
                        lastName={this.state.providerData.lastName}
                        streetaddress={this.state.providerData.streetaddress}
                        locality={this.state.providerData.locality}
                        region={this.state.providerData.region}
                        country={this.state.providerData.country}
                        zip_code={this.props.location.state.zip_code}
                        finalAppointmentPayload={state.finalAppointmentPayload}
                        getDataFromStep3={this.getDataFromStep3}
                        providerData={this.props.location.state.providerData}
                        showStepZillaNavigation={this.showStepZillaNavigation}
                    //firstName={this.props.location.state.firstName}
                    //token = {this.props.token}
                    />
                )
            },
            {
                name: 'Review & Confirm',
                component: (
                    <Step4
                        //userid={this.props.location.state.userid}
                        // userid={this.userid}
                        finalAppointmentPayload={state.finalAppointmentPayload}
                        provider_id={this.state.provider_id}
                        firstName={this.state.providerData.firstName}
                        middleName={this.state.providerData.middleName}
                        lastName={this.state.providerData.lastName}
                        streetaddress={this.props.location.state.streetaddress}
                        locality={this.props.location.state.locality}
                        region={this.props.location.state.region}
                        country={this.props.location.state.country}
                        zip_code={this.props.location.state.zip_code}
                        providerData={this.props.location.state.providerData}
                        showStepZillaNavigation={this.showStepZillaNavigation}
                    // user_data={this.state.user_data}
                    />
                )
            }
        ];
        this.setState({
            steps: steps
        });
    };

    render() {
        return (
            <div>
                <Header />
                <div className="profile appointment">
                    <article className="top-search-panel">
                        <div className="container-fluid">
                            <div>
                                <div className="row">
                                    <div className="col-md-6 col-sm-6">
                                        <ul className="breadcum text-left">
                                            <li>
                                                <a href="#">Arizona</a>
                                            </li>
                                            <li>
                                                <a href="#">85225</a>
                                            </li>
                                            <li>
                                                <a href="#">
                                                    {this.state.providerFirstName}{' '}
                                                    {this.state.providerMiddleName}{' '}
                                                    {this.state.providerLastName}
                                                </a>
                                            </li>
                                        </ul>
                                    </div>
                                    <div className="col-md-6 text-right">
                                        <ul className="social-links">
                                            <li>
                                                <a href="#">
                                                    <i className="fab fa-facebook-f" />
                                                </a>
                                            </li>
                                            <li>
                                                <a href="#">
                                                    <i className="fab fa-twitter" />
                                                </a>
                                            </li>
                                            <li>
                                                <a href="#">
                                                    <i className="fab fa-google-plus-g" />
                                                </a>
                                            </li>
                                            <li>
                                                <a href="#">
                                                    <i className="fas fa-envelope" />
                                                </a>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </article>
                    <div className="therapist-profile text-left">
                        <a href="#" className="text-primary">
                            <i className="fas fa-angle-left" />
                            &nbsp;Back</a>
                        <div className="intro">
                            <div className="row">
                                <div className="col-md-6">
                                    <h2>
                                        {this.state.providerFirstName} {this.state.providerMiddleName}{' '}
                                        {this.state.providerLastName}
                                    </h2>
                                    {/* <h2>Lynda griff</h2> */}
                                    {/* <h2>Lynda Griffin Bull, MC, LPC</h2> */}
                                    <small>
                                        Counselor/Therapist |{' '}
                                        <span className="active">
                                            {this.state.providerAddressRegion},{' '}
                                            {this.state.providerAddressCountry}
                                        </span>
                                    </small>
                                </div>
                                <div className="col-md-6 text-right">
                                    {/* <h2 className="active">(480) 409-2429</h2> */}
                                    <h2 className="active">{this.state.phone}</h2>
                                    {/* <h2>7575757</h2> */}
                                </div>
                            </div>
                        </div>
                        <div className="therapist-tab">
                            <div className="step-progress">
                                {this.state.steps.length > 0 ? (
                                    <StepZilla
                                        steps={this.state.steps}
                                        showNavigation={this.state.showNavigation}
                                    />
                                ) : null}
                            </div>
                            {/* {=======================} */}
                            <ExpertProfile providerData={this.state.providerData} />
                            {/* {=======================} */}
                        </div>
                    </div>
                    {/* Modal data */}

                    {/* Modal data */}
                </div>
            </div>
        );
    }
}

export default withRouter(Appointment);