import React, { Component } from 'react';
import {GetAuth} from '../../Sevices/GetAuth'
import {PostAuth} from '../../Sevices/PostAuth'
import { withRouter } from 'react-router'
class Setting_provider extends Component {
    constructor(props){
        super(props)
        this.state={
            appointmentreminder_email:null,
            appointmentreminder_sms:null,
            appointmentreminder_push:null,
            notification_email:null,
            notification_sms:null,
            notification_push:null,
           
        }
    }
  
    getSettingsHandler = () => {
        GetAuth(`user/getprofilesetting`).then((result) => {
            let responseJSON = result;
            console.log("-=-=-=-=-=-=-=-=", responseJSON)
            if (responseJSON) {
                this.setState(
                    {
                    appointmentreminder_email:responseJSON.data.appointmentreminder.email,
                    appointmentreminder_sms:responseJSON.data.appointmentreminder.sms,
                    appointmentreminder_push:responseJSON.data.appointmentreminder.push,
                    notification_email:responseJSON.data.notification.email,
                    notification_sms:responseJSON.data.notification.sms,
                    notification_push:responseJSON.data.notification.push,
                    
                }
                    )
            } 
            else {
                alert("No response")
            }

        })
    }
    componentDidMount(){
        this.getSettingsHandler()
    }

    handleInputChange = (event)  => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
      
           this.setState({
           [name]: value
           });
        }


    updateSettingsHandler = () => {
            var settings={
            appointmentreminder : {
               email : this.state.appointmentreminder_email,
               sms : this.state.appointmentreminder_sms,
               push : this.state.appointmentreminder_push
           },
           notification : {
               email : this.state.notification_email,
               sms : this.state.notification_sms,
               push : this.state.notification_push
           },
           pinNumber:1234
        }
        PostAuth('user/profilesetting', settings).then((result) => {
            let responseJSON = result;
            console.log("Response is here", responseJSON)
            if (responseJSON) {
                console.log("msg are here", responseJSON.message)
            } else {
                alert("Some error encountered")
            }

        }).then(()=>{
            this.getSettingsHandler()
        })
        alert("User Settings Updated Succesfully")
    }
    render() {
      
        return (
            <div className="pdsetting">
                <div className="setting-control text-left">
                    <h5 className="text-primary">Appointment Reminders</h5>
                    <ul className="settinglist">
                        <li className="text-right">
                            <span className="pull-left">Email</span>
                            <label className="switch">
                                <input type="checkbox" id="appEmail" name="appointmentreminder_email" onChange={this.handleInputChange} checked ={this.state.appointmentreminder_email===true}/>
                                <span className="slider round" htmlFor="appEmail"></span>
                            </label>
                        </li>
                        <li className="text-right">
                            <span className="pull-left">SMS (Text)</span>
                            <label className="switch">
                                <input type="checkbox" name="appointmentreminder_sms" onChange={this.handleInputChange} checked={this.state.appointmentreminder_sms===true} />
                                <span className="slider round"></span>
                            </label>
                        </li>
                        <li className="text-right">
                            <span className="pull-left">Push</span>
                            <label className="switch">
                                <input type="checkbox" name="appointmentreminder_push" onChange={this.handleInputChange} checked={this.state.appointmentreminder_push===true} />
                                <span className="slider round"></span>
                            </label>
                        </li>
                    </ul>
                </div>
                <div className="setting-control text-left">
                    <h5 className="text-primary">Notifications</h5>
                    <ul className="settinglist">
                        <li className="text-right">
                            <span className="pull-left">Email</span>
                            <label className="switch">
                                <input type="checkbox" name="notification_email" onChange={this.handleInputChange} checked={this.state.notification_email===true}/>
                                <span className="slider round"></span>
                            </label>
                        </li>
                        <li className="text-right">
                            <span className="pull-left">SMS (Text)</span>
                            <label className="switch">
                                <input type="checkbox" name="notification_sms"  onChange={this.handleInputChange} checked={this.state.notification_sms===true} />
                                <span className="slider round"></span>
                            </label>
                        </li>
                        <li className="text-right">
                            <span className="pull-left">Push</span>
                            <label className="switch">
                                <input type="checkbox" name="notification_push" onChange={this.handleInputChange} checked={this.state.notification_push===true}/>
                                <span className="slider round"></span>
                            </label>
                        </li>
                    </ul>
                </div>
                <div className="setting-control text-left">
                    <h5 className="text-primary">Notifications</h5>
                    <p>Your Pin Number is used to check-in at your appointments as well as account identification.</p>
                    <div className="input-group pincode">
                        {/* <input type="password" maxLength="1" className="form-control" /> */}
                        <input type="password"  maxLength="1" className="form-control" 
                        autoFocus={true}
                        returnKeyType={"next"}
                        onSubmitEditing={() => {
                        this.secondTextInput.focus();
                        }}
                        onChange={() => {
                        this.secondTextInput.focus();
                        }}
                        blurOnSubmit={false}
                     
                       />
                        <input type="password"  maxLength="1" className="form-control" 
                       
                         returnKeyType='next'
                         ref={input => {
                           this.secondTextInput = input;
                         }}
                         onSubmitEditing={() => {
                           this.thirdTextInput.focus();
                         }}
                         onChange={() => {
                           this.thirdTextInput.focus();
                         }}
                         blurOnSubmit={false}
                        />
                        <input type="password" maxLength="1" className="form-control"
                       
                         ref={input => {
                            this.thirdTextInput = input;
                          }}
                          returnKeyType={"next"}
                          onSubmitEditing={() => {
                            this.fourthTextInput.focus();
                          }}
                          onChange={() => {
                            this.fourthTextInput.focus();
                          }}
                          blurOnSubmit={false}
                        />
                        <input type="password" maxLength="1" className="form-control" 
                     
                          ref={input => {
                          this.fourthTextInput = input;
                        }}
                        returnKeyType={"done"}
                        
                        />
                    </div>
                    <br></br>
                    <button className="btn btn-success btn-lg" onClick={this.updateSettingsHandler}>UPDATE MY SETTINGS</button>
                </div>
            </div>
        );
    }
}

export default Setting_provider;