import React, { Component } from 'react';
import axios from 'axios'
import Loader from 'react-loader-spinner'
import moment from 'moment'
import { scheduleMyAppointmentApi } from './api'
import { asyncLocalStorage } from '../../common/helper';
import { USER_DATA } from '../../Utility/Constant'

class Step4 extends Component {
    constructor(props) {
        super(props)
        this.state = {
            thankyouPage: false,
            getattendeedata: {},
            checked: false,
            userId: '',
            providerId: '',
            providerData: {},
            finalAppointmentPayload: {},
            payment_info: {},
            providerFirstName: '',
            providerMiddleName: '',
            providerLastname: '',
            providerAddress: {},
            customerFirstName: '',
            customerMiddleName: '',
            customerLastName: '',
            customerDOB: '',
            appointmentDisplayDate: '',
            appointmentTime: '',
            appointmentLatitude: 0,
            appointmentLongitude: 0,
            user_data: {},
            isChecked: false
        }
    }

    componentDidMount() {
        window.scrollTo(0, 0)
        asyncLocalStorage.getItem(USER_DATA)
            .then(data => {
                if (JSON.parse(data)) {
                    const user_data = JSON.parse(data)
                    const providerData = this.props.providerData;
                    const finalAppointmentPayload = this.props.finalAppointmentPayload
                    console.log(providerData, finalAppointmentPayload, JSON.parse(data))
                    // this.setState({
                    //     user_data: JSON.parse(data)
                    // });
                    if (user_data != undefined && providerData != undefined && finalAppointmentPayload != undefined) {
                        this.updateFields(providerData, finalAppointmentPayload, user_data)
                    }
                }
            }).catch(error => {
                console.log(error)
            })
    }

    updateFields = (providerData, finalAppointmentPayload, user_data) => {


        let address = finalAppointmentPayload.appointment_location != undefined ? finalAppointmentPayload.appointment_location.address : {}
        let latLongArray = address != undefined && address != {} && finalAppointmentPayload.appointment_location.type == 'Office' ? address.location : [0, 0]
        this.setState({
            userId: user_data._id,
            providerId: providerData._id,
            providerData: providerData,
            finalAppointmentPayload: finalAppointmentPayload,
            providerFirstName: providerData.firstName,
            providerMiddleName: providerData.middleName,
            providerLastname: providerData.lastName,
            providerAddress: address,
            customerFirstName: user_data.firstName,
            customerMiddleName: user_data.middleName,
            customerLastName: user_data.lastName,
            customerDOB: moment(user_data.dob).format('DD/MM/YYYY'),
            customerEmail: user_data.email,
            appointmentDisplayDate: moment(finalAppointmentPayload.appointment_date).format("MMMM, DD YYYY"),
            appointmentTime: finalAppointmentPayload.scheduled_start_time + ' - ' + finalAppointmentPayload.scheduled_end_time,
            appointmentLatitude: latLongArray[1],
            appointmentLongitude: latLongArray[0]
        }, () => {

        });
    }
    onNextStepBtnClick = () => {

        if (this.state.isChecked) {
            this.setState({
                loading: false
            });
            let payment_info = {
                card_number: "4492-8272-2837-2838",
                card_type: "master card",
                card_expiry: "12/20",
                card_cvv: 4321,
                billing_zipcode: "64800-1123"
            }
            let finalAppointmentPayload = this.state.finalAppointmentPayload
            finalAppointmentPayload.userId = this.state.userId
            finalAppointmentPayload.providerId = this.state.providerId
            finalAppointmentPayload.payment_info = payment_info

            console.log('finalAppointmentPayload ', finalAppointmentPayload)

            scheduleMyAppointmentApi(finalAppointmentPayload)
                .then(scheduleAppointment => {
                    console.log('scheduleAppointment ', scheduleAppointment)
                    // this.showToast(scheduleAppointment.data.message)
                    if (scheduleAppointment.data.success) {
                        this.setState({ thankyouPage: true }, () => {
                            this.props.showStepZillaNavigation(false)
                        })

                        // this.props.navigation.navigate('BAThanksScreen', { providerData: this.state.providerData, appointmentData: scheduleAppointment.data.data })
                    } else {
                        this.setState({
                            loading: false
                        });
                    }
                })
                .catch(err => {
                    this.showToast(JSON.stringify(err))
                })
            // this.props.navigation.navigate('BAThanksScreen')
        } else {
            this.showToast('Accept Cancelation Policy')
        }

    }

    handleCheckBox = () => {
        console.log("====>>>")
        this.setState({
            isChecked: !this.state.isChecked
        })
    }

    ToggleHandler = (e) => {
        e.preventDefault();
        if (this.state.checked) {
            this.setState({ thankyouPage: true })
        }
        else {
            alert("Accept Cancellation Policy")
        }

    }
    handleChange = () => {
        this.setState({ checked: !this.state.checked })
    }
    showToast = (message) => {
        alert(message)
        // this.refs.toast.show(message, DURATION.LENGTH_LONG, () => {
        //     this.setState({ loading: false });
        // })
    }





    render() {
        return (
            <div>
                {this.state.finalAppointmentPayload != null ?
                    <div className="step4">
                        {this.state.thankyouPage ?
                            <div className="thankyou">
                                <h2 className="text-primary"><b>Thanks, {this.state.customerFirstName}!</b> See you soon :)</h2>
                                <p>An appointment confirmation email has been sent to:<br />{this.state.customerEmail}</p>
                                <div className="row">
                                    <div className="col-md-6">
                                        <hr></hr>
                                        <div className="content">
                                            <h1 className="active">Download Our Mobile App! </h1>
                                            <p className="active">Search, book and manage your therapy appointments right from your mobile device using the HopJax Mobile App.</p>
                                            <p>
                                                <img src={'/images/playStore_img.png'} /> &nbsp; <img src={'/images/appStore_img.png'} />
                                            </p>
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <figure className="mobile_demo">
                                            <img src={'/images/mobile_app_img.png'} />
                                        </figure>
                                    </div>
                                </div>
                            </div>
                            :
                            <div className="continer-wrapper">
                                <h2 className="text-center text-primary">{this.state.customerFirstName}, please review and confirm your details below.</h2>
                                {/* <h2 className="text-center text-primary">Hello, please review and confirm your details below.</h2> */}
                                <div className="cencellation">
                                    <div className="row">
                                        <div className="col-md-6">
                                            <div className="card">
                                                <h5 className="text-primary">Cancelation Policy</h5>
                                                <p>If an appointment is canceled within 24 hours of the scheduled time, a $75 Fee will be charged.<br />If you do not show up for your scheduled appointment, the full amount will be charged.</p>
                                                <p>
                                                    You may cancel your appointment by calling our office at (480) 409-2429. If you call outside our normal business hours, please leave a message.</p>
                                                <label className="text-danger">
                                                    YOUR SESSION FEE WILL NOT BE CHARGED UNTIL TIME OF SERVICE.</label>
                                                <div className="form">
                                                    <h5 className="text-primary">Payment Information</h5>
                                                    <div className="form-group">
                                                        <input type="text" className="form-control" placeholder="Card Number" />
                                                    </div>
                                                    <div className="form-group">
                                                        <img src={'/images/pay_options.png'} />
                                                    </div>
                                                    <div className="row">
                                                        <div className="col-lg-6">
                                                            <div className="form-group">
                                                                <input type="text" className="form-control" placeholder="EXP Date:  MM/YY" />
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6">
                                                            <div className="form-group">
                                                                <input type="text" className="form-control" placeholder="CVV (3 or 4 Digit Code)" />
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-6">
                                                            <div className="form-group">
                                                                <input type="text" className="form-control" placeholder="Billing Zip Code" />
                                                            </div>
                                                        </div>
                                                        <div className="col-lg-12">
                                                            <div className="form-group">
                                                                <label className="checkbox">
                                                                    <input type="checkbox"
                                                                        checked={this.state.isChecked}
                                                                        onChange={this.handleCheckBox} />
                                                                    <span>Ive read and understand the cancelation policy.</span>
                                                                </label>
                                                            </div>
                                                            <div className="form-group">
                                                                <button className="btn btn-default" style={{ minWidth: '120px' }}><i className="fas fa-angle-left"></i>&nbsp;&nbsp;&nbsp;&nbsp;Back</button>
                                                                &nbsp;&nbsp;  <button className="btn btn-primary"
                                                                    //onClick={this.state.thankyouPage = !this.state.thankyouPage} 
                                                                    onClick={this.onNextStepBtnClick}
                                                                >Schedule My Appointment</button>
                                                            </div>
                                                        </div>
                                                    </div>

                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-md-6">
                                            <div className="details">
                                                <h4 className="text-primary">Your Appointment Details</h4>
                                                <div className="media">
                                                    <span className="media-left  media-middle"><i className="far fa-building text-primary"></i></span>
                                                    <p className="media-body">
                                                        <label> {this.props.firstName} {this.props.middleName} {this.props.lastName}</label>{this.props.streetaddress},{this.props.locality}<br />{this.props.country} {this.props.zip_code}<br />
                                                        {/* <label> vndvinidndndndk mkdmk</label>sndvinv,vnnvihcvhvhiv <br /> 2521452<br /> */}
                                                        <figure>
                                                            <img src={'/images/map.png'} />
                                                        </figure>
                                                    </p>
                                                </div>
                                                <div className="media">
                                                    <span className="media-left media-middle"><i className="far fa-calendar text-primary"></i></span>
                                                    <p className="media-body">
                                                        {this.state.appointmentDisplayDate}
                                                    </p>
                                                </div>
                                                <div className="media">
                                                    <span className="media-left media-middle"><i className="far fa-clock text-primary"></i></span>
                                                    <p className="media-body">
                                                        {this.state.appointmentTime}
                                                    </p>
                                                </div>
                                                <div className="media">
                                                    <span className="media-left media-middle"><i className="far fa-user text-primary"></i></span>
                                                    <p className="media-body">
                                                        <label>{this.state.customerFirstName} {this.state.customerMiddleName} {this.state.customerLastName}</label>
                                                        {/* <label>John WIck</label> */}
                                                        DOB: {this.state.customerDOB}
                                                    </p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        }
                    </div> :
                    <div >
                        <Loader
                            type="Puff"
                            color="#00BFFF"
                            height="100"
                            width="100"
                        />
                    </div>}
            </div>

        );
    }
}

export default Step4;