import React, { Component } from 'react';
import { PostAuth } from '../../Sevices/PostAuth'
import { withRouter } from 'react-router'
class Reset_password extends Component {
    constructor(props) {
        super(props)
        this.state = {
            oldPassword: "",
            newPassword: "",
            confirmPassword: ""
        }
    }

    onChangeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })

    }

    updatePasswordHandler = (e) => {
        e.preventDefault(e)
        var passwordData = {
            oldpassword: this.state.oldPassword,
            password: this.state.confirmPassword,

        }
        if (this.state.newPassword === this.state.confirmPassword) {

            PostAuth('user/changepassword', passwordData).then((result) => {
                let responseJSON = result;
                console.log("Response is here", responseJSON)
                if (responseJSON.message === 'Your password successfully changed') {
                    this.setState({
                        oldPassword: "",
                        newPassword: "",
                        confirmPassword: ""
                    }, alert(responseJSON.message))
                    //alert(responseJSON.message)
                } else if (responseJSON.message === "Old password is invalid") {
                    alert(responseJSON.message)
                }
                else {
                    alert(responseJSON.message)
                }

            })
        }
        else {
            alert("Passwords must match")
        }
        // const { router } = this.props
        // router.push('/setting')
        //this.props.router.push('/setting')


    }
    render() {
        return (

            <div className="reset-pass">
                <form>
                    <div className="form-group">
                        <label className="form-inline">Old Password</label>
                        <input type="password" className="form-control" name="oldPassword" id='oldPassword' value={this.state.oldPassword} onChange={this.onChangeHandler} />
                        {/* <input type="text" className="form-control" /> */}
                    </div>
                    <div className="form-group">
                        <label className="form-inline">New Password</label>
                        <input type="password" className="form-control" name="newPassword" id='newPassword' value={this.state.newPassword} onChange={this.onChangeHandler} />
                        {/* <input type="text" className="form-control" /> */}
                    </div>
                    <div className="form-group">
                        <label className="form-inline">Confirm New Password</label>
                        {/* <input type="text" className="form-control" /> */}
                        <input type="password" className="form-control" name="confirmPassword" id='confirmPassword' value={this.state.confirmPassword} onChange={this.onChangeHandler} />
                    </div>
                    <div className="form-group">
                        <button className="btn btn-success" onClick={this.updatePasswordHandler}>UPDATE MY PASSWORD</button>
                    </div>

                </form>
            </div>
        );
    }
}

export default withRouter(Reset_password);